  },
  data () {
    return {
      
    };
  },

  methods: {
    
  }
}
</script>

<style scoped>
.description-title{
  margin-bottom: 20px;
  color: rgba(0,0,0,0.85);
  font-weight: bold;
  font-size: 16px;
  line-height: 1.5;
}
.content-wrapper{
  display: flex;
  flex-wrap: wrap;
}
.content-wrapper>div{
  flex: 0 0 33.33%;
}
</style>


