import { setting2domTree } from '../utils/setting2domTree'
import { attrs2attribute } from '../utils/attrs2attribute'
import { elementCountInArray } from '../utils/array'
import { ok } from '../utils/assertion'
import { replaceTag } from '../tools/dom/processTag'
import { compoVariableNameMap } from '../consts/variable'
import { rawTemplate2tsx, rawTemplate2vue, data2tsOrVue, data2ts} from '../utils/codeTransfer'
import { gatherLanguageByTraverseTree } from '../tools/locale/gatherLanguage'
const START_ID = 1
let uid = START_ID
const rootId = uid++
// stateTreerootNode
const rootEle = {
  tag: 'div',
  id: rootId,
  data: {
    style: {
      width: '100%',
      'min-height': '200px',
      border: '1px solid #ccc'
    }
  },
  children: []
}
export default class DndIview {
  constructor(data, id) {
    // this.rootEle = _.cloneDeep(rootEle)
    this.rootEle = data ? data : rootEle
    if (id) {
      uid = id
    }
    // this.template_like = ''
    // this.data_like = {}
  }
  // 
  getNodeById(id) {
    function search(node, parent){
      if(node.id === id) {
        return node
      }
      if(node.children) {
        for(let i = 0; i < node.children.length; i++) {
          let _node = search(node.children[i], node)
          if (_node) {
            return _node
          }
        }
      }
    }
    let _node = search(this.rootEle)
    return _node ? _node : null
  }
  // parent
  getNodeAndParent(id){
    function search(node, parent){
      if(node.id === id) {
        return {
          _node: node,
          _parent: parent ? parent : null
        }
      }
      if(node.children) {
        for(let i = 0; i < node.children.length; i++) {
          let obj = search(node.children[i], node)
          if (obj) {
            return {
              _node: obj._node,
              _parent: obj._parent
            }
          }
        }
      }
    }
    let { _node, _parent } = search(this.rootEle)
    console.log(_node, _parent)
    return {
      node: _node,
      parent: _parent
    }
  }
  // 
  /* allowAdd (targetNode, curNode) {
    if (!targetNode.allowAdd) {
      return false
    }
    return true
  } */
  // ,id
  addItem (addingNode, targetNode, mode) {
    const id = uid++
    addingNode.id = id

    if (!mode || mode === 'after') {
      ok(!targetNode.parent, 'Root Element can only added inside!')
      addingNode.parent = targetNode.parent
      const parentNode = this.getNodeById(targetNode.parent)
      const children = parentNode.children
      for (let i = 0; i < children.length; i ++) {
        if (children[i].id === targetNode.id){
          children.splice(i + 1, 0, addingNode)
        }
      }
    } else {
      //  to-do
      this.checkAdd(addingNode.tag, targetNode.tag)

      addingNode.parent = targetNode.id
      if (!targetNode.children) {
        targetNode.children = [addingNode]
      } else {
        targetNode.children.push(addingNode)
      }
    }
    return id
  }
  // 
  copyItem(id) {
    let { node, parent } = this.getNodeAndParent(id);
    let temp = _.cloneDeep(node)
    temp.id = uid++;
    function updateId(node) {
      if (node.children) {
        node.children.forEach(item => {
          item.id = uid++;
          if (Array.isArray(item.children)) {
            updateId(item)
          }
        })
      }
    }
    updateId(temp);
    // to-do,
    parent.children.push(temp)
  }
  // id
  updateChildrenId(node) {
    if (node.children) {
      node.children.forEach(item => {
        item.id = uid++;
        if (Array.isArray(item.children)) {
          this.updateChildrenId(item)
        }
      })
    }
  }
  // target
  checkAdd(tag, targetTag){
    const blackList = ['Divider', 'Button', 'Tabs',
      'Input', 'RadioGroup', 'CheckboxGroup', 'Select', 'Descriptions']
    ok(blackList.indexOf(targetTag) > -1, 'Invalid Injection!')
  }
  // (id)
  deleteItem (id) {
    ok(id === START_ID, 'Root element cannot be deleted!')
    let { node, parent } = this.getNodeAndParent(id);
    for (let i = 0; i < parent.children.length; i++) {
      if (parent.children[i].id === id) {
        parent.children.splice(i, 1)
      }
    }
    return parent.id
  }
  //  down up
  moveItem(id, direction = 'down') {
    let { node, parent } = this.getNodeAndParent(id);
    const children = parent.children
    const length = children.length
    for (let i = 0; i < length; i++) {
      if(children[i].id === id) {
        if (direction === 'down' && i < length - 1) {
          let _node = children.splice(i, 1)
          children.splice(i + 1, 0, ..._node)
        }
        if (direction === 'up' && i > 0) {
          let _node = children.splice(i, 1)
          children.splice(i - 1, 0, ..._node)
        }
        break;
      }
    }
  }
  // 
  clearChildren(id) {
    let target = this.getNodeById(id)
    target.children = []
  }
  // 
  getCode(tree) {
    const tagArr = []; // tag,formtable(regConst)
    let _rootEle = null
    let _data = {}
    const root = tree
    function getNodeCode (node, parent) {
      const Tag = node.tag;
      let count = elementCountInArray(Tag, tagArr)
      let ele = document.createElement(node.name ? node.name : node.tag)
      // propdata
      let setting = node.data.setting
      setting2domTree(ele, _data, setting, Tag, count)
      attrs2attribute(ele, _data, node, Tag, count)
      // let styles = {
      //   ...node.data.styleCopy,
      //   ...node.data.style
      // }
      // if (styles && node.id !== 1) {
      //   let stylesValue = ''
      //   for(let key in styles) {
      //     stylesValue += `${key}:${styles[key]};`
      //   }
      //   if (stylesValue) {
      //     ele.setAttribute('style', stylesValue)
      //   }
      // }
      if (!parent) {
        _rootEle = ele
      } else {
        parent.append(ele)
      }
      if (Array.isArray(node.children)) {
        node.children.forEach(child => {
          getNodeCode(child, ele)
        })
      }
      // hack: form
      if (Tag === 'Form') {
        const formVModelValue = ele.getAttribute('v-model')
        const formVModelVariable = formVModelValue.substring(1, formVModelValue.length)
        const formRuleValue = ele.getAttribute('rules')
        const formRuleVariable = formRuleValue.substring(1, formRuleValue.length)
        let specialArr = [] // nametype
        const formitems = ele.getElementsByTagName('i-form-item')
        const needVModelCompos = ['i-input', 'i-select', 'i-checkbox-group', 'i-radio-group']
        for (let i = 0; i < formitems.length; i++) {
          let item = formitems[i]
          let name = item.getAttribute('prop')
          let type
          if (name && item.children.length > 0) {
            let input_ele = item.children[0] // input,select,checkboxgroup,radiogroup
            type = input_ele.tagName.toLowerCase()
            if (needVModelCompos.indexOf(type) > -1) {
              input_ele.setAttribute('v-model', `@${formVModelVariable}.${name}`)
              specialArr.push({
                name,
                type
              })
            }
          }
        }
        specialArr.map(single => {
          console.log(single)
          _data[formVModelVariable][single.name] = ''
          _data[formRuleVariable][single.name] = [
            {
              required: true,
              message: `${single.name} is required`,
              trigger: single.type === 'i-input' ? 'blur' : 'change'
            }
          ]
        })
      }
      // hack table
      if (Tag === 'Table') {
        const children = ele.children;
        if (children.length) {
          _data[`scopedSlots${count ? count : ''}`] = {}
          for (let i = 0; i < children.length; i++) {
            const item = children[i]
            const name = item.getAttribute('slot')
            if (name) {
              _data[`scopedSlots${count ? count : ''}`][name] = replaceTag(item.outerHTML, 'slot', 'div')
            }
          }
          ele.setAttribute('scopedSlots', `@scopedSlots${count ? count : ''}`)
          ele.innerHTML = ''
        }
      }
      tagArr.push(Tag);
    }
    getNodeCode(root, _rootEle)
    // this.template_like = _rootEle
    // this.data_like = _data
    return {
      html: _rootEle,
      data: _data
    } 
  }
  // vue
  getVueCode() {
    const { html, data } = this.getCode()
    return {
      html: rawTemplate2vue(html.outerHTML, 'vue'),
      data: data2tsOrVue(data, 'vue')
    }
  }

  getTsCode() {
    const { html, data } = this.getCode()
    return {
      html: rawTemplate2tsx(html.outerHTML, 'tsx'),
      data: data2ts(data)
    }
  }

  // get state tree
  getStateTree(){
    return this.rootEle
  }

  init() {
    uid = 1
    this.rootEle = {
      tag: 'div',
      id: uid++,
      data: {
        style: {
          width: '100%',
          'min-height': '200px',
          border: '1px solid #ccc'
        }
      },
      children: []
    }
  }
  
  getUid() {
    return uid;
  }

  test() {
    const { languageObj, newTree } = gatherLanguageByTraverseTree(this.rootEle);
    const { html, data } = this.getCode(newTree)
    console.log(html, data, languageObj)
    return {
      html: rawTemplate2tsx(html.outerHTML, 'tsx'),
      dataStr: data2ts(data),
      data,
    }
  }
}


