const formatJs = require('js-beautify')
// key
export function formatObj(obj) {
  let str = JSON.stringify(obj)
  let re = /"(\w+)":/g
  str = str.replace(re, (all, $1) => {
    return $1 + ':'
  })
  str = str.replace(/"/g, "'")
  return formatJs(str)
}


