import Stack from '../utils/Stack'
const FLAG = '#'
const Errors = {
  structrueError: {
    type: 1,
    msg: 'Invalid Structrue'
  },
  duplicatedKeyError: {
    type: 2,
    msg: 'Duplicated Key'
  }
}
// return # to-improve
export function getPoundNum (item) {
  let re = /^(#+)/
  let arr = item.match(re)
  return arr && arr.length > 0 ? arr[0].length : 0
}

// #
function isFirstNode(str) {
  return getPoundNum(str) === 1
}

// key#
function validKey (str) {
  return str.replace(/^(#+)/, '')
}

// esonText -> arr(, )
export function esonText2arr (esonText) {
  let iMixMode = false // 
  let arr = esonText.split('\n')
  let _tempArr = []
  arr.forEach(item => {
    item = item.trim()
    if (item) {
      _tempArr.push(item)
      if (item.startsWith('#')) {
        iMixMode = true
      }
    }
  })
  return {
    esonArr: _tempArr,
    iMixMode
  }
}

// 5
// 5. in##(1, n + 1)
export function validStructrue (arr, iMixMode) {
  let parent = null
  /* if (iMixMode && !isFirstNode(arr[0])) {
    throw new Error('#')
  } */
  for (let i = 0; i < arr.length; i++) {
    let node = arr[i]
    if (node.startsWith('#')) {
      if (!parent) {
        parent = node
      } else {
        let parentPoundNum = getPoundNum(parent)
        let nodePoundNum = getPoundNum(node)
        if (nodePoundNum > parentPoundNum + 1) {
          throw new Error(`Invalid Structrue:${node}`)
        }
        parent = node
      }
    }
  }
}

// key
function hasDuplicatedKey(node, curNode) {
  for (let i = 0; i < node.children.length; i++) {
    let item = node.children[i]
    if (item.key === curNode.key) {
      throw new Error(Errors.duplicatedKeyError.msg + ':' + curNode.name)
    }
  }
}
// node
function createNode (item) {
  let node = {}
  if (item.startsWith('#')) {
    node.isLeafNode = false
    node.name = item
    node.key = validKey(item)
    node.children = []
  } else {
    const re = /^([\w\s]+):?([\w\s\S]+)?$/
    let _arr = item.match(re)
    if (!_arr) {
      throw new Error('key is empty:' + item)
    } else {
      let key = _arr[1]
      let value = _arr[2] ? _arr[2] : ''
      node.isLeafNode = true
      node.name = item
      node.key = key.trim()
      node.value = value.trim()
    }
  }
  return node
}
// rootNode => json
function rebuildJson (node) {
  let json = {}
  node.children.forEach(item => {
    if (item.isLeafNode) {
      json[item.key] = item.value
    } else {
      if (item.children) {
        json[item.key] = rebuildJson(item)
      }
    }
  })
  return json
}
// 8,rootObject {key:'', children:[]}
export function eson2json (arr) {
  // arr -> root
  const root = {
    name: 'root',
    children: []
  }
  const stack = new Stack()
  for (let i = 0; i < arr.length; i++) {
    let node = createNode(arr[i])
    // 
    let topNode = stack.getTop()
    if (node.isLeafNode) {
      if (!topNode) {
        //  to-do
        hasDuplicatedKey(root, node)
        root.children.push(node)
      } else {
        hasDuplicatedKey(topNode, node)
        topNode.children.push(node)
      }
    } else {
      let poundNum = getPoundNum(node.name)
      if (!topNode) {
        stack.push(node)
        // 
        hasDuplicatedKey(root, node)
        root.children.push(node)
      } else {
        let topNodeNum = getPoundNum(topNode.name)
        if (poundNum > topNodeNum + 1) {
          throw new Error(Errors.structrueError.msg)
        } else if (poundNum === topNodeNum + 1) {
          stack.push(node)
          // 
          hasDuplicatedKey(topNode, node)
          topNode.children.push(node)
        } else {
          while (topNodeNum >= poundNum) {
            stack.pop()
            topNode = stack.getTop()
            topNodeNum = topNode ? getPoundNum(topNode.name) : 0
          }
          if (!topNode) {
            // 
            hasDuplicatedKey(root, node)
            root.children.push(node)
          } else {
            // 
            hasDuplicatedKey(topNode, node)
            topNode.children.push(node)
          }
          stack.push(node)
        }
      }
    }
  }
  // root -> json
  let json = rebuildJson(root)
  return json
}
// arr -> json
export function arr2json(arr, iMixMode) {
  let json = {}
  if (!iMixMode) {
    arr.forEach(item => {
      let _temp = item.split(':') // a:33
      json[_temp[0]] = _temp[1]
    })
    return json
  }
  let stack = new Stack()
  for (let i = 0; i < arr.length; i++) {
    let item = arr[i]
    let topNode = stack.getTop()
    if (item.startsWith('#')) {
      let num = getPoundNum(item)
      let key = validKey(item)
      // ,
      if (topNode === null) {
        stack.push({
          name: item,
          children: {},
          key: validKey(item)
        })
      } else {
        let topNum = getPoundNum(topNode.name)
        if (topNum === num) {
          let outNode = stack.pop()
          // 
          if (outNode.name === item) {
            throw new Error(`Repeat key: ${item}`)
          }
          // outNode#
          if (getPoundNum(outNode.name) === 1) {
            let key = validKey(outNode.name)
            json[key] = outNode.children
          } else {
            let topNode = stack.getTop()
            topNode.children = Object.assign(topNode.children, {
              [outNode.key]: outNode.children
            })
          }
        }
        if (topNum > num) {
          while (topNum >= num) {
            let outNode = stack.pop()
            // 
            if (topNum === num) {
              console.log(outNode, item)
              if (outNode.name === item) {
                throw new Error(`Repeat key: ${item}`)
              }
            }
            // outNode#
            if (getPoundNum(outNode.name) === 1) {
              let key = validKey(outNode.name)
              json[key] = outNode.children
              topNum = 0
            } else {
              let topNode = stack.getTop()
              topNode.children = Object.assign(topNode.children, {
                [outNode.key]: outNode.children
              })
              topNum = getPoundNum(stack.getTop().name)
            }
          }
        }
        stack.push({
          name: item,
          children: {},
          key: validKey(item)
        })
      }
    } else {
      let _temp = item.split(':') // a:33
      if (topNode) {
        topNode.children = Object.assign(topNode.children, {
          [_temp[0]]: _temp[1]
        })
      } else {
        json[_temp[0]] = _temp[1]
      }
    }
  }
  while (stack.getLength() > 0) {
    let outNode = stack.pop()
    if (isFirstNode(outNode.name)) {
      let key = validKey(outNode.name)
      json[key] = outNode.children
    } else {
      let nextNode = stack.getTop()
      nextNode.children = Object.assign(nextNode.children, {
        [outNode.key]: outNode.children
      })
    }
  }
  /* console.log(stack) */
  /* let root = stack.getTop()
  json[root.key] = root.children */
  return json
}


