export function typeOf(s){
  const map = {
    "[object Object]": 'object',
    "[object String]": 'string',
    "[object Number]": 'number',
    "[object Null]": 'null',
    "[object Array]": 'array',
    "[object Boolean]": 'boolean',
  }
  return map[Object.prototype.toString.call(s)]
}
