// 32id
export function generateUUID() {
  var d = new Date().getTime();
  var uuid = 'xxxxxxxx-xxxx-4xxx-yxxx-xxxxxxxxxxxx'.replace(/[xy]/g, function(c) {
    var r = (d + Math.random()*16)%16 | 0;
    d = Math.floor(d/16);
    return (c=='x' ? r : (r&0x3|0x8)).toString(16);
  });
  return uuid;
}

// Find components upward
function findComponentUpward (context, componentName, componentNames) {
  if (typeof componentName === 'string') {
      componentNames = [componentName];
  } else {
      componentNames = componentName;
  }

  let parent = context.$parent;
  let name = parent.$options.name;
  while (parent && (!name || componentNames.indexOf(name) < 0)) {
      parent = parent.$parent;
      if (parent) name = parent.$options.name;
  }
  return parent;
}
export {findComponentUpward};

// Find component downward
export function findComponentDownward (context, componentName) {
  const childrens = context.$children;
  let children = null;

  if (childrens.length) {
      for (const child of childrens) {
          const name = child.$options.name;
          if (name === componentName) {
              children = child;
              break;
          } else {
              children = findComponentDownward(child, componentName);
              if (children) break;
          }
      }
  }
  return children;
}

// Find components downward
export function findComponentsDownward (context, componentName) {
  return context.$children.reduce((components, child) => {
      if (child.$options.name === componentName) components.push(child);
      const foundChilds = findComponentsDownward(child, componentName);
      return components.concat(foundChilds);
  }, []);
}

// Find components upward
export function findComponentsUpward (context, componentName) {
  let parents = [];
  const parent = context.$parent;
  if (parent) {
      if (parent.$options.name === componentName) parents.push(parent);
      return parents.concat(findComponentsUpward(parent, componentName));
  } else {
      return [];
  }
}

// Find brothers components
export function findBrothersComponents (context, componentName, exceptMe = true) {
  let res = context.$parent.$children.filter(item => {
      return item.$options.name === componentName;
  });
  let index = res.findIndex(item => item._uid === context._uid);
  if (exceptMe) res.splice(index, 1);
  return res;
}

// json
export function downloadAsJson (code, filename) {
  /* let oPre = document.createElement('pre')
  oPre.innerHTML = code
  console.log(oPre.innerHTML, typeof oPre.innerHTML) */
  let jCode = JSON.stringify(code, null, '  ')
  let file = new File([jCode], '')
  let oA = document.createElement('a')
  oA.href = window.URL.createObjectURL(file)
  oA.download = filename.endsWith('.json') ? filename : filename + '.json'
  oA.click()
}
// 
export function formatObj(obj) {
  for (let key in obj) {
    if (obj[key] === '') {
      delete obj[key]
    }
  }
  return obj
}


