// divcol
const specialComponent = ['col']
const inlineComponents = ['Col', 'Button', 'Divider', 'a']
const showBorderComponents = ['FormItem', 'Row', 'slot', 'Descriptions']
import { hasClass } from '../utils/dom'
import { formatObj } from '../utils/assist'
import { needBordComponents } from '../consts/iviewConst'

import { renderAssist } from '../utils/render/renderAssist'

function _h(h, ctx, node) {
  function renderEntity2(node) {
    /*  console.log(node, typeof node !== 'object') */
    if(typeof node !== 'object') {
      return node
    } else {
      if(!node.data){
        node.data = {}
      }
      return h(node.tag, {
        style: {
          ...node.data.style,
          border: node.id === ctx.props.activeId ? '1px solid #2d8cf0' : '1px dashed #2d8cf0',
          'min-height': '40px'
        },
        class: 'item-wrapper',
        domProps: {
          id: node.id,
        },
        props: _.cloneDeep(node.data.props),
        on: {
          click: (e) => {
            e.stopPropagation()
            console.log('[item click]', e.target);
            let curEle = e.target
            while(!hasClass(curEle, 'item-wrapper')){
              curEle = curEle.parentNode
            }
            ctx.listeners.changeActiveId(curEle.getAttribute('id'))
          }
        }
      }, [renderChildren(h, ctx, node), renderAssist(h, ctx, node)])
    }
  }
  function renderEntity(node) {
    /*  console.log(node, typeof node !== 'object') */
    if(typeof node !== 'object') {
      return node
    } else {
      if(!node.data){
        node.data = {}
      }
      return h(node.tag === 'slot' ? 'div' : node.tag, {
        style: node.data.style,
        class: node.data.class,
        props: _.cloneDeep(node.data.props),
      }, [renderChildren(h, ctx, node)])
    }
  }
  if (specialComponent.indexOf(node.tag.toLowerCase()) !== -1) {
    return renderEntity2(node)
  }
  let activated = false;
  /* console.log(renderEntity(h, node),  '------------------') */
  let slotObj = {}
  if (node.tag === 'slot') {
    slotObj = {
      slot: node.data.setting.slot.value,
    }
  }
  return h('div',{ 
    style: {
      border: ctx.props.activeId == node.id ? '1px solid #2d8cf0' :
        showBorderComponents.indexOf(node.tag) > -1 ? '1px solid #eee' : 'none',
      position: 'relative',
      /* display: node.tag === 'Col' ? 'inline-block': 'block', */
      'min-height': '34px',
      'min-width': '20px',
      display: inlineComponents.indexOf(node.tag) > -1 ? 'inline-block': 'block',
      ...node.data.styleCopy
    },
    class: 'item-wrapper',
    ...slotObj,
    domProps: {
      id: node.id,
    },
    x: 2222,
    on: {
      click: (e) => {
        e.stopPropagation()
        console.log('[item click]', e.target);
        let curEle = e.target
        while(!hasClass(curEle, 'item-wrapper')){
          curEle = curEle.parentNode
        }
        ctx.listeners.changeActiveId(curEle.getAttribute('id'))
      }
    }
  },[renderEntity(node), renderAssist(h, ctx, node)])
}
function renderChildren(h, ctx, node){
  if (node.text) { // 
    //return _h(h, node.text)
    return node.text
  } else if (node.children) { // Default as array to-do
    let children = node.children
    if (Array.isArray(children)) {
      return children.map(item => {
        if (!item.tag){
          return item.text
        } else {
          return _h(h, ctx, item)
        }
      })
    } else { // v-for
      let { tag, data } = children
      return data.map(item => {
        return h(tag, {
          props: item
        }, item.text ? item.text : '')
      })
    }
  }
}
export default {
  functional: true,
  props: {
    /* render: Function, */
    root: Object,
    activeId: Number
  },
  render: (h, ctx) => {
    const root = ctx.props.root
    /* console.log(root, '[Render render]') */
    /* return h('Table', {
      props: {
        columns: [{
          title: '',
          slot: 'name'
        },{
          title: '',
          key: 'a'
        }],
        data: [{
          a: 1,
          name: '2222'
        }],
      },
      scopedSlots: {
        name: (props) => {
          return h('div',{}, 1111222)
        }
      }
    }, [
      h('div', {
        slot: 'name',
      }, [h('p', '@22')])
    ]) */
    return _h(h, ctx, root)
  }
}



