import Vue from 'vue'
import Router from 'vue-router'
import arrayObject from '@/components/arrayObject'
import test from '@/pages/test'
import buffer from '@/pages/buffer'
import json1 from '@/pages/json1'
import test1 from '@/pages/test1'
import test2 from '@/pages/test2'
import file from '@/pages/file'
import easyJson from '@/pages/easyJson'
import tree from '@/pages/tree'
import table from '@/pages/table'
import easyIview from '@/pages/easyIview'
import fragment from '@/pages/fragment'
import JSONHandle from '@/pages/JSONHandle'
import multi from '@/pages/multi'
import pageList from '@/pages/PageList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '*',
      redirect: '/page-list',
    },
    // {
    //   path: '/',
    //   // name: 'arrayObject',
    //   component: pageList
    // },
    {
      path: '/test',
      component: test
    },{
      path: '/buffer',
      component: buffer
    },
    {
      path: '/json',
      component: JSONHandle
    },
    {
      path: '/json1',
      component: json1
    },
    {
      path: '/test1',
      component: test1
    },
    {
      path: '/test2',
      component: test2
    },
    {
      path: '/file',
      component: file
    },
    {
      path: '/page',
      component: easyIview
    },
    {
      path: '/page/:id',
      component: easyIview
    },
    {
      path: '/frag',
      component: fragment,
      children: [
        {
          path: ':type',
          component: file
        }
      ]
    },
    {
      path: '/easyJson',
      component: easyJson
    },
    {
      path: '/multi',
      component: multi
    },
    {
      path: '/page-list',
      component: pageList
    }
  ]
})



