<template>
  <div class="pre-wrapper">
    <div class="pre-wrapper-operate">
      <img src="../assets/open.png" alt="" width="20px" @click="showCodeModal" class="img">
      <img src="../assets/copy.png" alt="" width="20px" @click="copy" class="img">
    </div>
    <pre v-if="extra">{{this.extra}}</pre>
    <pre ref="pre">{{ this.code }}</pre>
    <!-- <div style="display:none">
      <img src="../assets/copy.png" alt="" width="20px" @click="copy" class="img" @load='xx'>
    </div> -->
    <Modal
      title=""
      v-model="showCode">
      <div class="pre-wrapper">
        <div class="pre-wrapper-operate">
          <img src="../assets/copy.png" alt="" width="20px" @click="copy" class="img">
        </div>
        <pre>{{ this.code }}</pre>
      </div>
      <a ref="download">
        <Button type="primary" style="margin-top: 20px"></Button>
      </a>
    </Modal>
  </div>
</template>

<script>
  import Clipboard from 'clipboard'
  export default {
    props: {
      extra: {
        type: String,
      },
      code: {
        type: Array|Object,
        require: false
      }
    },
    data () {
      return {
        showCode: false
      }
    },
    methods: {
      copy () {
        let val = this.$refs.pre.innerHTML
        const clipboard = new Clipboard('.img', {
          text () {
            return val
          }
        })
        clipboard.on('success', e => {
          this.$Message.success('Copied!')
          clipboard.destroy()
        })
      },
      showCodeModal () {
        this.showCode = true
        //let blob = new Blob([this.$refs.pre.innerHTML])
        //let b = `const columns = ${this.$refs.pre.innerHTML}`
        let file = new File([this.$refs.pre.innerHTML], '')
        let a = this.$refs.download
        a.href=window.URL.createObjectURL(file)
        a.download = '1.json'
      },
      xx () {
      }
    }
  }
</script>

<style scoped>
  .pre-wrapper{
    background: #283646;
    padding: 40px;
    position: relative;
    height: 100%;
    overflow-y: auto;
  }
  .pre-wrapper-operate{
    position: absolute;
    right: 5px;
    top: 5px;
  }
  pre{
    text-align:left;
    color:white
  }
</style>


