<template>
  <div>
    <div class="test"><a href="javascript:void(0)" class="icon-wrapper"></a>
          <!-- <img
            :src="node_img"
            alt="close"
            class="icon"
            title="close"/> -->
      <!-- <span>34</span> -->
      <span>huashi</span>
    </div>
    <div class="huashi">
      <img
        :src="node_img"
        alt="close"
        width="100px"
        height="100px"
        title="close"/>
        erer
    </div>
    <!-- <test>Hua 
      <p>re</p>
    </test> -->
  </div>
</template>

<script>
import test from '@/components/test'
  export default {
    components: {
      test
    },
    data () {
      return {
        node_img: require('../assets/copy.png'),
      }
    }
  }
</script>

<style scoped>
.huashi{
  border: 1px solid red;
}
.wrapper{
  display: flex;
  flex-direction: column;
  width: 300px;
  height: 300px;
  background: rgb(200, 207, 214);
}
.test{
  box-sizing: border-box;
  width: 300px;
  height: 30px;
  background: rgb(211, 172, 223);
  font-size: 0;
}
span:last-child{
  display: inline-block;
  line-height: 30px;
  font-size: 14px;
}
.icon-wrapper{
  display: inline-block;
  width: 16px;
  height: 30px;
  font-size: 14px;
}
.icon{
  display: inline-block;
  width: 16px;
  height: 30px;
  cursor: row-resize;
}
</style>


