/*
* desc: @/#
* @param{String} rawTemplate
  <i-form label-width="60" label-position="left" v-model="@dd" rules="@re">
    <i-form-item prop="a" label='#invite'><i-input v-model="@dd.a" type="text"></i-input></i-form-item>
  </i-form>
* @return {String} tsx or vue template(default vue)
*/
import { LOCALE_FLAG } from '../consts/LocaleFlag'
import { processTemplateLanguage, processDataLanguage } from '../tools/locale/locale'
import { useSingleQuate } from '../utils/format/singleQuate'
import { replaceTag, camelTag } from '../tools/dom/processTag'
const formatJs = require('js-beautify')
const htmlForMat = require('js-beautify').html

export function rawTemplate2tsx(rawTemplate) {
  // i-xx  Xx
  rawTemplate = camelTag(rawTemplate)

  // @
  const re = /"@([.a-zA-Z1-9]+)"/g
  rawTemplate = rawTemplate.replace(re, (all, $1) => {
    return `{this.${$1}}`
  })

  // 
  rawTemplate = processTemplateLanguage(rawTemplate)

  // tag slot -> div;
  rawTemplate = replaceTag(rawTemplate, 'slot', 'div')

  // (custom-setting)
  rawTemplate = rawTemplate.replace(/"/g, "'")

  // 
  rawTemplate = htmlForMat(rawTemplate)
  return rawTemplate;
}

export function rawTemplate2vue(rawTemplate) {
  rawTemplate = camelTag(rawTemplate)
  const re = /((v-)?\w+)="@([1-9A-Za-z.]+)"/g
  rawTemplate =  rawTemplate.replace(re, (all, $1, $2, $3) => {
    if ($1.indexOf('v-') > -1) {
      return `${$1}='${$3}'`;
    } else {
      return `:${$1}='${$3}'`;
    }
  })
  const re_language_prop = /(\w+)="#([A-Za-z.]+)"/g
  rawTemplate = rawTemplate.replace(re_language_prop, (all, $1, $2) => {
    return `:${$1}="$t('${$2}')"`
  })
  const re_languge_text = /#(\w+)/g
  rawTemplate = rawTemplate.replace(re_languge_text, (all, $1) => {
    return `{{ $t('${$1}') }}`
  })
  // slot tag template
  rawTemplate = rawTemplate.replace(/<(\/)?slot/g, (all, $1) => {
    return `<${$1 ? $1 : ''}template`;
  })
  // (custom-setting)
  rawTemplate = rawTemplate.replace(/"/g, "'")
  // 
  rawTemplate = htmlForMat(rawTemplate)
  return rawTemplate
}


export function data2ts(data) {
  // scopedSlots
  for (let key in data) {
    if (key.startsWith('scopedSlots')) {
      const obj = data[key]
      for (let i in obj) {
        const ele = obj[i]
        obj[i] = useSingleQuate(camelTag(processTemplateLanguage(`(props: any) => { return ${ele} }`)))
      }
    }
  }
  // 
  let dataStr = JSON.stringify(data)
  dataStr = processDataLanguage(dataStr)

  let re = /"(\w+)":/g
  dataStr = dataStr.replace(re, (all, $1) => {
    return $1 + ':'
  })
  // to-do,
  //dataStr = dataStr.replace(/"/g, "'")
  // dataStr = dataStr.replace(/'(\.+)}'/, (all, $1) => {
  //   return `@${$1}}`
  // })
  let dataCode = formatJs(dataStr)
  return dataCode
}

export function data2tsOrVue(data, target) {
  let dataStr = JSON.stringify(data);
  // to-do(key)
  let re = /"(\w+)":/g
  dataStr = dataStr.replace(re, (all, $1) => {
    return $1 + ':'
  })
  dataStr = dataStr.replace(/"/g, "'")
  let dataCode = formatJs(dataStr)
  return dataCode
}



