<template>
  <div class=wrapper>
    <div class='left-bar'></div>
    <div class='middle-content'>
      <Row class="middle-title"></Row>
      <Alert closable>
        <Button to="https://shimo.im/docs/JqwTkHXXYtYjc6Jj" target="_blank" type="success"></Button>
        <Button to="https://shimo.im/docs/yGh96pWXC9RtpptP" target="_blank" type='warning'>Bug</Button>
      </Alert>
      <Row :gutter='48'>
        <Col :span='6' v-for="(item, index) in pages" :key='index' class-name='item-wrapper'>
          <div
            class='item-content'
            @click='handlePageClick(item)'
            v-contextmenu:contextmenu
            @contextmenu="test(item)">
            <div class="item-content-page">
              <div class='item-image'></div>
              <div class='item-name'>{{item.name}}</div>
            </div>
          </div>
        </Col>
      </Row>
      <v-contextmenu ref="contextmenu" style="padding: 10px 20px">
        <v-contextmenu-item @click='handlePageOpen'></v-contextmenu-item>
        <v-contextmenu-item @click='handlePageRename'>
          <!-- <Icon type="ios-create-outline" size='18' style="margin-right: 10px"/> -->
          
        </v-contextmenu-item>
        <v-contextmenu-item @click="handlePageDelete"></v-contextmenu-item>
      </v-contextmenu>
    </div>
    <div class='right-operation'>
      <Row style='margin-bottom: 24px'>
        <Button type='primary' long @click='newPage'></Button>
      </Row>
      <Row>
        <Button long></Button>
      </Row>
      <Modal
        v-model="showNewPageModal"
        title=""
        :loading="newPageLoding"
        @on-ok="handlePageNew">
        <Input v-model="pageName" maxlength='15' style="margin-top: 5px"></Input>
      </Modal>
      <Modal
        v-model="showRenamePageModal"
        title=""
        @on-ok="handlePageRenameModalOk">
        <Input v-model="renameName" maxlength='15' style="margin-top: 5px"></Input>
      </Modal>
    </div>
  </div>
</template>

<script>
import codeBlock from '../base/codeBlock'
import MultiLanguageEditor from '../base/MultiLanguageEditor'
import { generateUUID, downloadAsJson } from '../utils/assist'
import PageApi from '../apis/PageApi'
import hackMixin from '../mixins/hackMixin'
export default {
  components: {
    MultiLanguageEditor,
    codeBlock
  },
  mixins: [hackMixin],
  data () {
    return {
      pages: [],
      showNewPageModal: false,
      showRenamePageModal: false,
      languageLoading: false,
      newPageLoding: false,
      pageName: '',
      renameName: '',
      languageCode: '',
      pageApi: null,
      pages: [],
    };
  },
  async created() {
    this.pageApi = new PageApi()
    await this.remoteGetPages();
    // 
    this.init()
  },
  methods: {
    newPage() {
      this.showNewPageModal = true;
    },
    handlePageClick(item) {
      this.$router.push(`page/${item.id}`);
    },
    handlePageNew() {
      if(this.pageName.trim() === ''){
        this.$Message.error('')
        this.newPageLoding = false
        this.$nextTick(() => {
          this.newPageLoding = true
        })
      } else {
        this.remoteAddPage()
      }
    },
    test (item) {
      this.curContextMenuPage = item
      console.log(item.id, 'context-menu')
    },
    handlePageDelete () {
      if (!this.curContextMenuPage) {
        return
      }
      this.$Modal.confirm({
        title: '?',
        content: '<p></p>',
        onOk: () => {
          this.remoteDeletePage()
        },
      })
    },
    handlePageRename() {
      this.showRenamePageModal = true
      this.renameName = this.curContextMenuPage.name
    },
    handlePageRenameModalOk() {
      this.curContextMenuPage.name = this.renameName
      this.remoteUpdatePage()
    },
    handlePageOpen() {
      window.open(`${location.host}/#/page/${this.curContextMenuPage.id}`, "_blank")
    },
    generatMultiLanguage() {
      this.languageLoading = true
      this.languageCode = this.$refs.languageEditor.change2json()
      this.curContextMenuPage.language = this.languageCode
      localStorage.setItem('pageList', JSON.stringify(this.pages))
    },
    // 
    async remoteAddPage() {
      try {
        await this.pageApi.addPage({
          name: this.pageName,
          data: {},
        })
        this.pageName = ''
        this.newPageModal = false
        this.$Message.success('')
        this.remoteGetPages()
      } catch (e) {
        console.log(e)
        this.$Message.error('add page fail')
      }
    },
    async remoteDeletePage() {
      try {
        await this.pageApi.deletePage(this.curContextMenuPage.id)
        this.remoteGetPages()
        this.$Message.success('');
      } catch (e) {
        console.log(e)
        this.$Message.error('');
      }
    },
    async remoteUpdatePage() {
      try {
        await this.pageApi.updatePage(this.curContextMenuPage.id, this.curContextMenuPage)
        this.$Message.success('');
      } catch (e) {
        console.log(e)
        this.$Message.error('');
      }
    },
    async remoteGetPages() {
      try {
        this.pages = await this.pageApi.getAllPages();
      } catch (e) {
        console.log(e, 'get pages fail')
        this.$Message.error('get pages fail');
      }
    }
  }
}
</script>

<style scoped>
.wrapper{
  display: flex;
}
.left-bar{
  width: 240px;
  border-right: 1px solid #e5e5e5;
}
.middle-content{
  padding: 0 24px;
  flex: 1;
  border-right: 1px solid #e5e5e5;
}
.middle-title{
  padding: 40px 20px 10px 20px;
  font-size: 18px;
  font-weight: 600;
}
.item-wrapper{
  height: 150px;
  padding: 15px;
}
.item-content{
  height: 100%;
  cursor: pointer;
  padding: 0 20px;
}
.item-image{
  height: 90px;
}
.item-name{
  height: 30px;
  line-height: 30px;
}
.item-content-page{
  background: #e5e5e5;
  height: 100%;
  text-align: center;
}
.right-operation{
  width: 200px;
  padding: 30px 20px;
}
</style>


