<template>
  <div>
    <p @click="test"></p>
    <div class="test" placeholder="red" a="999">test</div>
    <router-view/> -->
    <!-- <input @change="input" v-model="xx" @keydown="x"/> -->
    <div v-for="(item, index) in arr" :key="index">
      <component :is="item.tag" :columns="columns"/>
    </div>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        arr: [
          {
            tag: 'Table'
          },
          {
            tag: 'Table'
          }
        ],
        columns: [
          {
            title: '2',
            key: 'number'
          },{
            title: '3',
            key: '34'
          },{
            title: '3',
            key: '34'
          },
          
        ],
        xx: ''
      }
    },
    mounted() {
      console.log(this.$children,this.$options)
      console.log('22',this.$parent)
    },
    methods: {
      test() {
        this.$router.push(`frag/aa`)
      },
      x(e){
        console.log(e.keyCode)
        if (this.xx.search(/^[0-9]+\.[0-9]$/) !== -1 && e.keyCode !== 8) {
          e.preventDefault()
        }
      }
    }
  }
</script>

<style scoped>
.test:before{
  content: attr(a);
  color: attr(placeholder);
}
</style>


