<template>
  <div class="json">
    <div class="json-input">
      <Button type="primary" @click="reset">JSON</Button>
    </div>
    <div class="json-show">
      <json-show :jdata="data"/>
    </div>
    <Modal v-model="showInputModal" title="JSON" @on-ok="handleOk">
      <Input
        v-model="custom_json"
        type="textarea"
        placeholder="Copy your json here!"
        :rows="10"/>
    </Modal>
  </div>
</template>

<script>
  import jsonShow from "@/base/jsonShow"
  export default {
    components: {
      jsonShow
    },
    data () {
      return {
        showInputModal: false,
        custom_json: '',
        data: {"a":23,"c":232,"ee": [[5,6,7],5,65],"d": [{a:{b:34,b:4444},b:43},54,5,656],"e":{"e":34,"f":{"name": "rerer",age:34},"d":[45,45,565,2]}}
      }
    },
    created(){
      // console.log(this.data)
      // this.data = JSON.stringify(this.data,null," 3 ")
      // console.log(this.data)
    },
    methods: {
      reset(){
        this.showInputModal = true
      },
      handleOk(){
        this.data = JSON.parse(this.custom_json)
        console.log(this.data)
      }
    }
  }
</script>

<style scoped>
.json{
  display: flex;
  text-align: left;
  background: #FAFAFF;
}
.json-input{
  width: 200px;
}
.json-show{
  flex: 1;
}
</style>



