import { LOCALE_FLAG } from '../../consts/LocaleFlag'
import { getCollectionItemLocale } from './getCollectionItemLocale'
import { extendObj } from './extendObj';
// easyJson
// @param tree: 
// @return ['#form', 'name:', 'type:']
const REG_ZH = /[\u4e00-\u9fa5]/
export function gatherLanguageByTraverseTree(tree) {
  let languageObj = {}
  // 
  function processNode(node) {
    if (node.data.setting) {
      const setting = node.data.setting
      for (let key in setting) {
        const item = setting[key]
        // text
        if (item.type === 'text') {
          if (REG_ZH.test(item.value)) {
            if (node.tag === 'FormItem') {
              let temp = setting.prop.value ? setting.prop.value : key
              let last_key = extendObj(languageObj, {
                key: temp,
                value: item.value
              })
              item.value = `${LOCALE_FLAG}${last_key}`
            } else {
              let temp = `${firstLetter2Lower(node.tag)}${firstLetter2Upper(key)}`
              let last_key = extendObj(languageObj, {
                key: temp,
                value: item.value
              })
              item.value = `${LOCALE_FLAG}${last_key}`
            }
          }
        }
        // collection
        if (item.type === 'collection') {
          item.value.map(o => {
            let {res, zh_key} = getCollectionItemLocale(o)
            if (res.value) {
              let last_key = extendObj(languageObj, res)
              o[zh_key] = `${LOCALE_FLAG}${last_key}`
            }
          })
        }
      }
    }
    if (node.children && Array.isArray(node.children)) {
      node.children.map(item => {
        processNode(item)
      })
    }
  }
  const new_tree = _.cloneDeep(tree)
  processNode(new_tree)
  // console.log(JSON.stringify(new_tree), 'tree')
  return {
    languageObj,
    newTree: new_tree
  }
}

function firstLetter2Upper(str) {
  return str ? str.replace(/(\w)/, (all, $1) => {
    return $1.toUpperCase()
  }) : ''
}

function firstLetter2Lower(str) {
  return str ? str.replace(/(\w)/, (all, $1) => {
    return $1.toLowerCase()
  }) : ''
}


