function _c(h, node) {
  if(typeof node !== 'object') {
    return node
  } else {
    if(!node.data){
      node.data = {}
    }
    if (node.tag === 'slot') {
      return h('div', {
        style: {
          ...node.data.style,
          ...node.data.styleCopy
        },
        slot: node.data.setting.slot.value,
        class: node.data.class,
        props: _.cloneDeep(node.data.props),
      }, [renderChildren(h, node)])
    } else {
      return h(node.tag, {
        style: {
          ...node.data.style,
          ...node.data.styleCopy
        },
        class: node.data.class,
        props: _.cloneDeep(node.data.props),
      }, [renderChildren(h, node)])
    }
    // const CommonProps = {
    //   style: {
    //     ...node.data.style,
    //     ...node.data.styleCopy
    //   },
    //   class: node.data.class,
    //   props: _.cloneDeep(node.data.props),
    // }
    // if (node.tag === 'slot') {
    //   return h('template', {
    //     ...Object.assign(CommonProps, {
    //       slot: node.data.setting.slot.value,
    //     })
    //   }, [renderChildren(h, node)])
    // } else {
    //   return h(node.tag, {
    //     ...CommonProps
    //   }, [renderChildren(h, node)])
    // }
  }
}
function renderChildren(h, node){
  if (node.text) { // 
    //return _h(h, node.text)
    return node.text
  } else if (node.children) {
    let children = node.children
    if (Array.isArray(children)) {
      return children.map(item => {
        if (!item.tag){
          return item.text
        } else {
          return _c(h, item)
        }
      })
    } else { // v-for
      let { tag, data } = children
      return data.map(item => {
        return h(tag, {
          props: item
        }, item.text ? item.text : '')
      })
    }
  }
}
export default {
  functional: true,
  props: {
    root: Object,
  },
  render: (h, ctx) => {
    const root = ctx.props.root
    return _c(h, root)
  }
}



