<template>
  <div>
  <div class="edit-div" contenteditable="true" @focus="test($event)"></div>
  </div>
</template>

<script>
  export default {
    components: {
    },
    data () {
      return {
      }
    },
    methods: {
      test(e){
        console.log('test', e, e.target.innerHTML)
       // if(!e.target)
       //  e.target.innerHTML = `<div class="row"><br/></div>`
      }
    }
  }
</script>

<style scoped>
.edit-div{
  text-align: left;
  width: 600px;
  height: 200px;
  border: 1px solid rgb(139, 138, 138);
}
</style>


