<template>
  <div class="array-object-wrapper">
    <div class="left">
      <div style="">
        <Button type="error" @click='clear' style="margin-right:10px"></Button>
        <Button type="primary" @click='changeToJSON'>JSON</Button>
      </div>
      <table border="0">
        <thead>
          <tr>
            <th v-for="(item, index) in ths" :key="index">
              <input type="text" v-model="ths[index]">
            </th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in data" :key="index">
            <td v-for="(kid, idx) in item" :key="idx">
              <input type="text" v-model="item[idx]">
            </td>
          </tr>
        </tbody>
      </table>
      <div style='font-size:16px;font-weight: 550'>
        <p>JSON</p>
        <p></p>
      </div>
    </div>
    <div class="right" @click="show">
      <code-block :code='code'></code-block>
    </div>
  </div>
</template>

<script>
  import codeBlock from '../base/codeBlock'
  const COLS = 10
  const ROWS = 15
  function isRepeated (arr) {
  for(let i = 0;i<arr.length;i++){
    if(arr.indexOf(arr[i])!=i){
      return true
    }
  }
  return false
}
  export default {
    components: {
      codeBlock
    },
    data () {
      return {
        ths: [], // 
        data: [], // 
        code: []
      }
    },
    created(){
      this.init()
    },
    methods: {
      show(){
        console.log(this.data)
      },
      clear(){
        this.init()
      },
      changeToJSON(){
        let temp = [] //
        let temp_data = [] //
        // this.ths,temp
        for(let i = 0; i < COLS - 1; i++){
          if(this.ths[i]==''&&this.ths[i+1]!=''){
            this.$Message.error('JSON')
            return
          }
        }
        for(let i = 0; i < COLS - 1; i++){
          if(this.ths[i] != ''){
            temp[i] = this.ths[i].trim()
          }
        }
        //temp
        if(isRepeated(temp)){
          this.$Message.error('JSON')
          return
        }
        if(temp.length == 0){
          this.$Message.error('JSON')
          return
        }
        // data,
        for(let i = 0; i < ROWS; i++){
          let obj = this.data[i]
          for(let j=0; j < temp.length; j++){
            if(obj['key' + j] != ''){
              temp_data.push(obj)
              break
            }
          }
        }
        this.code = []
        for(let i = 0; i < temp_data.length; i++){ //
          let obj = temp_data[i]
          let res = {}
          for(let j=0; j < temp.length; j++){  //
            res[temp[j]] = obj['key' + j].trim()
          }
          this.code.push(res)
        }
        
      },
      init(){
        this.ths = new Array(10)
        this.code = []
        for(let j=0; j < COLS; j++){
          this.ths[j] = ''
        }
        this.data = []
        for(let i = 0; i < ROWS; i++){
          let obj = {}
          for(let j=0; j < COLS; j++){
            obj['key'+j] = ''
          }
          this.data.push(obj)
        }
      }
    }
  }
</script>

<style scoped>
  .array-object-wrapper{
    height: calc(100vh - 50px);
    display: flex;
  }
  .left{
    background: #ccc;
    flex: 1;
    height: 100%;
    padding-top: 40px;
  }
  table{
    border: 0;
    margin: 20px auto;
    border-collapse: collapse;
  }
  input{
    width: 60px;
    height: 25px;
    font-size: 16px;
  }
  th>input{
    background: rgb(87, 196, 247);
    color: white;
  }
  td>input{
    background: rgb(218, 213, 210);
  }
  .right{
    flex: 1;
    overflow: auto;
    padding: 40px;
  }
</style>


