import { compoVariableNameMap } from '../consts/variable'
export function attrs2attribute(ele, data, node, tag, count) {
  // const customNames = JSON.parse(localStorage.getItem('defaultNames'))
  const { setting, attrs } = node.data;
  for (let key in attrs) {
    const item = attrs[key]
    let variableName = `${compoVariableNameMap[tag][key]}${count ? count : ''}`;
    ele.setAttribute(key, `@${variableName}`);
    if (item.configurable) {
      data[variableName] = setting[key].value
    } else {
      if (item.value !== undefined && item.value !== '') { // fix bug 12/8 tableLoading
        data[variableName] = item.value
      }
    }
  }
}



