<template>
  <div class="wrapper">
    <div class="left-bar">
      <!-- <div v-for="(item, index) in iviewCompos" :key="index" class="left-component-item" @click="dndClick(item.tag)">
        <div class="left-component-item-img"></div>
        <div class="left-component-item-title">{{ item.tag }}</div>
      </div> -->
      <div class="left-bar-title"></div>
      <div class="left-bar-tree">
        <!-- <tree-show v-if="rootTree" :tree='rootTree'/>
        test: -->
        <tree-show2
          v-if="rootTree"
          :tree='rootTree'
          :activeId='curActiveItemId'
          @changeActiveId="handleActiveIdChange"/>
      </div>
    </div>
    <div class="middle">
      <div class="top-bar">
        <Button class='button-item' type="primary" @click="clear"></Button>
        <Button class='button-item' type="primary" icon='ios-bookmark' @click="save"></Button>
        <Button class='button-item' type="primary" icon='ios-eye' @click="preview"></Button>
        <!-- <Button class='button-item' type="primary" @click="showCode">(Vue)</Button> -->
        <Button class='button-item' type="primary" @click="showLanguage" :loading='showLanguageLoading'>
          
        </Button>
        <Button class='button-item' type="primary" @click="showTsCode">(TS)</Button>
        <Modal
          v-model="showCodeModal"
          title="Code">
          <Row style="padding: 10px 0">
            <span style="line-height: 30px;margin-right: 20px">Template:</span>
            <!-- <RadioGroup v-model="templateType" type="button" size='small'>
              <Radio label="tsx">Tsx</Radio>
              <Radio label="vue">Vue</Radio>
            </RadioGroup> -->
          </Row>
          <code-block :code="code"/>
          <p style="line-height: 30px">data:</p>
          <Alert type="warning" closable show-icon>any</Alert>
          <code-block :extra='extra' :code="dataCode"/>
        </Modal>
        <Modal
          v-model="showLanguageModal"
          title="">
          <code-block :code='languageCode'/>
        </Modal>
        <Modal
          v-model="showPreviewModal"
          title=""
          fullscreen>
          <RenderShow v-if="rootTree" :root="rootTree"/>
        </Modal>
        <!-- Modal -->
        <Modal
          v-model="showCompoModal"
          title="">
          <div style="margin-bottom: 10px">
            
            <RadioGroup v-model="injectType">
              <Radio label="after" :disabled='!allowInjectAfter'>After</Radio>
              <Radio label="inside">Inside</Radio>
            </RadioGroup>
          </div>
          <div class="modal-comp-select">
            <div v-for="(children, index) in iviewCompos2" :key="index" class=''>
              <div style="font-weight: 600">{{ children.title }}:</div>
              <div class="modal-comp-select">
                <div
                  v-for="(item, id) in children.components"
                  :key="item.tag"
                  :class="{'active-item': curSelectedCompoTag === item.tag}"
                  :title="item.tag"
                  class="left-component-item"
                  @click="dndClick2(item.tag, index)">
                  <div class="left-component-item-img"></div>
                  <div class="left-component-item-title">{{ item.tag }}</div>
                </div>
                <!-- <Divider/> -->
              </div>
            </div>
          </div>
          <div slot="footer">
            <Button
              @click="cancelModalShow('showCompoModal')">
              
            </Button>
            <Button
              :disabled='curSelectedCompoTag === -1'
              type='primary'
              @click="handleCompoAdd">
              
            </Button>
          </div>
        </Modal>
      </div>
      <div class="create-page-wrapper" v-if="rootTree && hackReset">
        <Render
          :root="rootTree"
          :activeId='curActiveItemId'
          @buttonClick='handleRenderButtonClick'
          @changeActiveId="handleActiveIdChange"
          @addItem="handleItemAdd"/>
      </div>
    </div>
    <div class="right-setting">
      <div class='setting-top'>
        <div
          @click="handleSetSwitch('data')"
          :class="{'setting-item': true, 'active-setting-item': curSettingType === 'data'}">
          <div class='setting-item-image'></div>
          <p></p>
        </div>
        <div class='setting-top-middle'
          @click="handleSetSwitch('style')"
          :class="{'setting-item': true, 'active-setting-item': curSettingType === 'style'}">
          <div class='setting-item-image'></div>
          <p></p>
        </div>
        <div @click="handleSetSwitch('other')"
          :class="{'setting-item': true, 'active-setting-item': curSettingType === 'other'}">
          <div class='setting-item-image'></div>
          <p>other</p>
        </div>
      </div>
      <div v-if="curSettingType === 'data'" style="margin: 20px">
        <data-setting :name='curSettingNodeTag' :component="curSelectedComponent" @custum-set='handleCustomSet'></data-setting>
      </div>
      <template v-else-if="curSettingType === 'style'">
        <!-- <style-setting :component="curSelectedComponent"></style-setting> -->
        <p></p>
      </template>
    </div>
  </div>
</template>

<script>

import StyleSetting from '@/components/dndIview/StyleSetting'
import DataSetting from '@/components/dndIview/DataSetting'
import codeBlock from '@/base/codeBlock'
import Render from '@/base/Render'
import RenderShow from '@/base/RenderShow'
import TreeShow from '@/components/dndIview/TreeShow'
import TreeShow2 from '@/components/dndIview/TreeShow2'

import { getObjTranslation } from '../apis/translate';
import { gatherLanguageByTraverseTree } from '../tools/locale/gatherLanguage'
import { formatObj } from '../utils/format/formatObj'

import { IviewCompos, getDataByTag, getTagName, IviewCompos2, createSlot } from '../consts/source'
import { actionMaps } from '../consts/renderButtonData'
import DndIview from '../tools/dndIview2'
import { root } from '../consts/node'
import { setting2componentProp } from '../utils/setting2componentProp'
import { typeOf } from '../utils/assist'
const formatHtml = require('js-beautify').html;
const formatJs = require('js-beautify');

import PageApi from '../apis/PageApi'

const Tag = 'easyIview'
export default {
  name: 'easyIview',
  components: {
    codeBlock,
    Render,
    RenderShow,
    StyleSetting,
    DataSetting,
    TreeShow,
    TreeShow2
  },
  // provide() {
  //   return {
  //     easyIviewData: this.provideData
  //   }
  // },
  // computed: {
  //   provideData() {
  //     return  {
  //       id: this.curActiveItemId
  //     }
  //   }
  // },
  data () {
    return {
      // core
      dndIview: null,
      // new
      hackReset: true,
      curSelectedComponent: null,
      /* root, */
      allowInjectAfter: true,
      rootTree: null,
      curActiveItemId: null,
      curSelectedCompoTag: -1,
      // old code
      iviewCompos: IviewCompos,
      iviewCompos2: IviewCompos2,
      // middle
      showCompoModal: false,
      injectType: 'after',
      showCodeModal: false,
      showLanguageModal: false,
      showLanguageLoading: false,
      languageCode: '',
      showPreviewModal: false,
      code: null,
      dataCode: null,
      extra: '',
      templateType: 'tsx',
      rawTemplate: '',
      rawData: {}, // 
      // 
      tagName: '',
      curSettingNodeTag: null,
      curSettingType: 'data',
      page: null,
    }
  },
  watch: {
    curActiveItemId: function (val){
      console.log(val, 'watch')
      this.curSelectedComponent = this.dndIview.getNodeById(val)
      console.log(this.dndIview.getNodeAndParent(val))
      //this.$forceUpdate()
      this.freshSetting()
    },
    rootTree(val) {
      console.log('------root tree change-------')
    },
  },
  async created () {
    this.pageApi = new PageApi()
    if (this.$route.params.id) {
      let tree
      let uid
      await this.getPage()
      if (!this.page) {
        this.$Message.error('')
        this.$router.push('/page-list')
        return
      }
      tree = this.page.data.ele
      uid = this.page.data.uid
      this.dndIview = new DndIview(tree, uid)
      this.rootTree = this.dndIview.getStateTree()
    }
    if (!this.$route.params.id) {
      this.dndIview = new DndIview()
      this.rootTree = this.dndIview.getStateTree()
    }
    console.log(Tag, this.rootTree, 'created')
  },
  methods: {
    getActiveCompoDetail(){
      return this.dndIview.getNodeAndParent(this.curActiveItemId)
    },
    // 
    handleSetSwitch(name){
      this.curSettingType = name
    },
    freshSetting(){
      const targetNode = this.dndIview.getNodeById(this.curActiveItemId)
      this.curSettingNodeTag = targetNode.tag
    },
    dndClick (tag) {
      this.$Message.info('')
    },
    preview() {
      this.showPreviewModal = true;
    },
    async showLanguage() {
      const {languageObj} = gatherLanguageByTraverseTree(this.rootTree)
      if (Object.keys(languageObj).length === 0) {
        this.$Message.info('')
        return
      }
      this.languageCode = {
        zh: languageObj,
        en: ''
      }
      let en_obj = await this.remoteGetObjTranslation(languageObj)
      if (en_obj) {
        this.languageCode.en = en_obj
      }
      this.languageCode = formatObj(this.languageCode)
      this.showLanguageModal = true
    },
    showCode() {
      let { html, data } = this.dndIview.getVueCode();
      this.code = html
      this.dataCode = data
      this.showCodeModal = true
    },
    showTsCode() {
      const { html, dataStr, data} = this.dndIview.test()
      this.code = html
      // this.dataCode = dataStr
      // test
      let str = ''
      for (let i in data) {
        let type = typeOf(data[i])
        if (['string', 'number', 'boolean'].indexOf(type) > -1) {
          type = type.replace(/(\w)/, (all, $1) => {
            return $1.toUpperCase()
          })
        } else if (type === 'array') {
          type = 'any[]'
        } else {
          type = 'any'
        }
        str += `private ${i}: ${type};`
      }
      str += `private data() {
        return ${dataStr}
      }`
      this.dataCode = formatJs(str);
      this.showCodeModal = true
    },
    clear(){
      this.dndIview.init()
      this.rootTree = this.dndIview.getStateTree()
      console.log(this.rootTree, 'new rootele')
    },
    save() {
      this.remoteUpdatePage(this.$route.params.id, {
        data: {
          ele: this.dndIview.getStateTree(),
          uid: this.dndIview.getUid()
        },
        name: this.page.name,
      })
    },
    // modal
    dndClick2 (tag, index) {
      // console.log('[compo click]', index)
      if (tag.indexOf('/') > 0) {
        return
      }
      this.curSelectedCompoTag = tag
      this.compoData = _.cloneDeep(getDataByTag(tag))
      this.showNewColumnModal = false
      // test
      let targetNode = this.dndIview.getNodeById(this.curActiveItemId)
      console.log('[compo data]', this.compoData, targetNode)
      let addingNode = _.clone(this.compoData)
      try{
        this.curActiveItemId = this.dndIview.addItem(addingNode, targetNode, this.injectType);
        this.rootTree = this.dndIview.getStateTree();
      } catch(e) {
        this.$Message.error(e.message)
      }
      this.showCompoModal = false
    },
    cancelModalShow (name) {
      this[name] = false
    },
    // Render
    handleRenderButtonClick (name) {
      this['handle' + actionMaps[name].action]()
    },
    handleActiveIdChange(val){
      console.log('emit event', val, typeof val)
      this.curActiveItemId = Number(val)
    },
    handleItemAdd () {
      // root disallow insert after
      if (this.curActiveItemId === 1) {
        this.allowInjectAfter = false
        this.injectType = 'inside'
      } else {
        this.allowInjectAfter = true
        // this.injectType = 'after'
      }
      this.curSelectedCompoTag = -1;
      this.showCompoModal = true
    },
    handleCompoAdd(){
      let targetNode = this.dndIview.getNodeById(this.curActiveItemId)
      console.log('[compo data]', this.compoData, targetNode)
      let addingNode = _.clone(this.compoData)
      try{
        this.curActiveItemId = this.dndIview.addItem(addingNode, targetNode, this.injectType);
        this.rootTree = this.dndIview.getStateTree()
      } catch(e) {
        this.$Message.error(e.message)
      }
      this.showCompoModal = false
    },
    handleItemDelete() {
      try {
        this.curActiveItemId = this.dndIview.deleteItem(this.curActiveItemId)
        this.rootTree = this.dndIview.getStateTree()
      } catch (e) {
        this.$Message.error(e.message)
      }
    },
    handleItemCopy(){
      this.dndIview.copyItem(this.curActiveItemId);
      this.rootTree = this.dndIview.getStateTree()
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    handleItemParent(){
      let { node, parent } = this.dndIview.getNodeAndParent(this.curActiveItemId)
      this.curActiveItemId = parent.id
    },
    handleItemUp(){
      this.dndIview.moveItem(this.curActiveItemId, 'up')
      this.rootTree = this.dndIview.getStateTree()
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    handleItemDown(){
      this.dndIview.moveItem(this.curActiveItemId, 'down')
      this.rootTree = this.dndIview.getStateTree()
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    handleItemClear() {
      this.dndIview.clearChildren(this.curActiveItemId)
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    // data
    handleCustomSet (tag) {
      let node = this.dndIview.getNodeById(this.curActiveItemId)
      setting2componentProp(node)
      // hack: tableslot
      if (tag === 'Table') {
        node.children = []
        // this.dndIview.updateChildrenId(node)
        node.data.setting.columns.value.map(column => {
          if (column.slot) {
            this.dndIview.addItem(createSlot(column.slot), node, 'inside')
          }
        })
      }
      this.hackReset = false
      this.$nextTick(() => {
        this.hackReset = true
      })
    },
    // 
    async remoteUpdatePage(id, page) {
      try {
        await this.pageApi.updatePage(id, page)
        this.$Message.success('');
      } catch (e) {
        console.log(e)
        this.$Message.error('');
      }
    },
    async getPage() {
      try {
        const id = this.$route.params.id
        this.page = await this.pageApi.getPage(id)
      } catch (e) {
        console.log(e)
        this.$Message.error('get page fail')
      }
    },
    async remoteGetObjTranslation(obj) {
      this.showLanguageLoading = true
      try {
        const result = await getObjTranslation(obj)
        this.showLanguageLoading = false
        return result.data.data
      } catch (e) {
        this.showLanguageLoading = false
        this.$Message.error('')
        console.log(e)
      }
    }
  }
}
</script>

<style>
/* .ivu-poptip-body-content{
  overflow: visible;
}
.ivu-table-wrapper,.ivu-table,.ivu-table-cell{
  overflow: visible;
} */
</style>

<style scoped>
.middle>>>.ivu-poptip-body-content{
  overflow: visible;
}
.middle>>>.ivu-table-wrapper,.middle>>>.ivu-table,.middle>>>.ivu-table-cell{
  overflow: visible;
}
.wrapper{
  display: flex;
}
.left-bar{
  /* display: flex;
  flex-wrap: wrap; */
  width: 240px;
  min-width: 240px;
  padding: 20px 10px;
  /* align-content: flex-start; */
}
.left-bar-title{
  font-weight: bold;
  padding-bottom: 5px;
  border-bottom: 1px solid #ddd;
}
.left-bar-tree{
  max-width: 220px;
  overflow: auto;
}
.modal-comp-select{
  display: flex;
  flex-wrap: wrap;
  width: 480px;
  align-content: flex-start;
}
.left-component-item{
  display: flex;
  flex-direction: column;
 /*  margin-bottom: 18px; */
  height: 100px;
  width: 120px;
  padding: 10px;
  
}
.left-component-item-compo{
  flex: 1;
  padding: 10px;
}
.left-component-item-img{
  width: 100%;
  background: #ccc;
  flex: 1;
}
.left-component-item-title{
  height: 20px;
  line-height: 20px;
  border: 1px solid #ccc;
  text-align: center;
}
.middle{
  box-sizing: border-box;
  flex: 1;
  height: 100%;
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.page-item{
  position: relative;
  border-left: 1px solid rgb(231, 229, 229);
  border-right: 1px solid rgb(231, 229, 229);
}
.active-item{
  border: 1px solid #2d8cf0;
  background-color: #f8f9fa!important;
}
.middle-compo-tip{
  display: block;
  position: absolute;
  top: -24px;
  line-height: 20px;
  left: 0;
  background: #2d8cf0;
  color: white;
  padding: 2px;
}
.middle-compo-add{
  display: block;
  position: absolute;
  bottom: -12px;
  line-height: 20px;
  left: 50%;
  background: #2d8cf0;
  z-index: 1000;
}
.middle-compo-operation{
  position: absolute;
  top: -26px;
  right: 0;
}
.top-bar{
  /* height: 50px; */
  text-align: left;
  padding: 12px 60px;
  border-bottom: 1px solid rgb(197, 195, 195);
}
.button-item{
  margin-right: 8px;
}
.right-setting{
  width: 260px;
  height: 100%;
}
.setting-top{
  display: flex;
}
.setting-item{
  padding: 10px 16px 8px 16px;
  flex: 1;
  text-align: center;
  border-bottom: 1px solid #ccc;
}
.active-setting-item{
  /* border: 1px solid #2db7f5; */
  color: #2db7f5;
  border-bottom: 1px solid #2db7f5;
}
.setting-top>div>.setting-item-image{
  height: 30px;
}
.setting-top-middle{
  border-left: 1px solid #ccc;
  border-right: 1px solid #ccc;
}
.create-page-wrapper{
  height: 400px;
}
</style>


