// settingprop
export function setting2componentProp(node) {
  const setting = node.data.setting
  const props = node.data.props
  for (let key in setting) {
    let item = setting[key]
    // isSlot
    if (key === 'text') {
      node.text = item.value
    } else {
      if (item.isSlot) {
        if(!node.children.length) {
          node.children.data = item.value
        } else {
          node.children[item.index].text = item.value // Alert
        }
      }
      if (!item.isSlot && item.value) {
        let variableKey = item.for ? item.for : key
        node.data.props[variableKey] = item.value
      }
    }
  }
}


