<template>
  <div>
    <table v-show="!bulkMode">
      <thead>
        <tr class="thead-tr" v-if="!keys || keys.length === 0">
          <th class="first-column"></th>
          <th class="content-column">KEY</th>
          <th class="content-column">VALUE</th>
        </tr>
        <tr class="thead-tr" v-else>
          <th class="first-column"></th>
          <th class="content-column" v-for="(item, index) in keys" :key="index">{{ item }}</th>
        </tr>
      </thead>
      <tbody>
           
      </tbody>
    </table>
  </div>
</template>

<script>
export default {
  components: {
  },
  props: {
    value: {
      type: Array,
      required: true
    },
    keys: {
      type: Array,
      /* default: () => {
        return ['Key', 'Value1']
      } */
    }
  },
  data () {
    return {
      bulkMode: false
    }
  }
}
</script>

<style scoped>
table{
  width: 100%;
  border-collapse: collapse;
  position: relative;
}
th{
  border: 1px solid#EAEAEA;
  height: 38px;
  line-height: 30px;
  text-align: left;
  padding-left: 5px;
}
td{
  border: 1px solid#EAEAEA;
  height: 48px;
  line-height: 30px;
  text-align: left;
  padding-left: 5px;
}
.first-column{
  width: 50px;
  text-align: right;
}
.content-column{
  min-width: 200px;
}
</style>



