import { actionMaps } from '../../consts/renderButtonData'
export function renderButton(h, ctx, props, style, name){
  const _props = {
    type: 'primary',
    size: 'small',
    icon: props.icon
  }
  if (props.shape) {
    _props.shape = props.shape
  }
  return h('Button', {
    props: _props,
    style: style,
    domProps: {
      title: actionMaps[name].title
    },
    on: {
      click: (e) => {
        e.stopPropagation()
        ctx.listeners.buttonClick(name)
      }
    }
  })
}


