import  '../../style/index.css'
export default {
  functional: true,
  props: {
    tree: Object,
    activeId: Number
  },
  render: (h, ctx) => {
    const tree = ctx.props.tree
    return _h(h, ctx, tree)
  }
}
function _h(h, ctx, tree){
  return <ul class='children-wrapper'>
    <li
      style={{'border': ctx.props.activeId === tree.id ? ' 1px solid #2db7f5' : 'none'}}
      onClick={() => {ctx.listeners.changeActiveId(tree.id)}}>{tree.tag}</li>
    {
      Array.isArray(tree.children) && tree.children.length > 0 && <ul>
        {
          tree.children.map(item => {
            return _h(h, ctx, item)
          })
        }
      </ul>
    }
  </ul>
}


