export const IviewCompos2 = [
  {
    title: '',
    components: BaseComponents
  },
  // {
  //   title: '',
  //   components: UnionComponents
  // },
  {
    title: '',
    components: LayoutComponents
  },
  {
    title: '',
    components: FormComponents
  },
  {
    title: '',
    components: NavigationComponents
  },
  {
    title: '',
    components: viewComponents
  }
]
export const IviewCompos = [
  ...BaseComponents,
  ...FormComponents,
  ...LayoutComponents,
  ...NavigationComponents,
  ...viewComponents
]

export function getDataByTag (tag) {
  for (let i = 0; i < IviewCompos.length; i++) {
    if (IviewCompos[i].tag === tag) {
      return IviewCompos[i]
    }
  }
  return null
}

export function getTagName (tag) {
  console.log(tag)
  tag = tag.toLowerCase()
  const maps = {
    table: '',
    tabs: '',
    card: '',
    modal: '',
    form: ''
  }
  return maps[tag]
}

export function createSlot(name) {
  return {
    tag: 'slot',
    data: {
      setting: {
        text: {
          type: 'text',
          value: '',
        },
        slot: {
          type: 'text',
          value: name ? name : '',
          required: true,
        }
      }
    },
    children: []
  }
}



