<template>
  <div class="style-setting-wrapper">
    <Collapse v-model="value1" accordion>
      <Panel name="margin">
        Margin:
        <div slot='content'>
          <div v-for="(item, index) in Margins" :key="index" class='item'>
          <div class="item-title">{{ item.title}}:</div>
          <Input
            v-model="margin[item.key]"
            type='text'
            class="item-input"
            @on-blur='handleInputBlur(`margin-${item.key}`, margin[item.key])'>
            <Select v-model="margin.measurement" slot="append" style="width: 70px">
              <Option value="px">px</Option>
              <Option value="rem">rem</Option>
              <Option value="pt">pt</Option>
              <Option value="em">em</Option>
              <Option value="percent">%</Option>
            </Select>
          </Input>
        </div>
        </div>
      </Panel>
      <Panel name="padding">
        Padding:
        <div slot='content'>
          <div v-for="(item, index) in Paddings" :key="index" class='item'>
          <div class="item-title">{{ item.title}}:</div>
          <Input
            v-model="padding[item.key]"
            type='text'
            class="item-input"
            @on-blur='handleInputBlur(`padding-${item.key}`, padding[item.key])'>
            <Select v-model="margin.measurement" slot="append" style="width: 70px">
              <Option value="px">px</Option>
              <Option value="rem">rem</Option>
              <Option value="pt">pt</Option>
            </Select>
          </Input>
        </div>
        </div>
      </Panel>
       <Panel name="text">
        Text
        <div slot='content'>
          <div v-for="(item, index) in Text" :key="index">
          <div class="item-title">{{ item.title}}:</div>
          <div>
            <RadioGroup v-model="textAlign" type="button" size='small' @on-change="handleTextAlignChange">
              <Radio label="left"></Radio>
              <Radio label="center"></Radio>
              <Radio label="right"></Radio>
            </RadioGroup>
          </div>
        </div>
        </div>
      </Panel>
    </Collapse>
  </div>
</template>

<script>
export default {
  props: {
    component: {
      type: Object
    }
  },
  data () {
    return {
      value1: '',
      textAlign: 'left',
      Margins: [
        {
          "key": "top",
          "title": "Top"
        },
        {
          "key": "right",
          "title": "Right"
        },
        {
          "key": "bottom",
          "title": " Bottom"
        },
        {
          "key": "left",
          "title": "Left"
        }
      ],
      margin: {
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        measurement: 'px',
      },
      Paddings: [
        {
          "key": "top",
          "title": "Top"
        },
        {
          "key": "right",
          "title": "Right"
        },
        {
          "key": "bottom",
          "title": " Bottom"
        },
        {
          "key": "left",
          "title": "Left"
        }
      ],
      Text: [{
        key: 'text-align',
        title: ''
      }],
      padding: {
        top: 0,
        right: 0,
        bottom: 0,
        left: 0,
        measurement: 'px',
      }
    };
  },

  methods: {
    handleInputBlur(key, value) {
      // margincomponent
      if (key.startsWith('margin')) {
        // documentidthis.component.idelement
        document.getElementById(this.component.id).style[key] = value + 'px'
        // 
        if (!this.component.data.styleCopy) {
          this.component.data.styleCopy = {}
        }
        this.component.data.styleCopy[key] = value + 'px'
      } else {
        if (!this.component.data.style) {
          this.component.data.style = {}
        }
        this.component.data.style[key] = value + 'px'
      }
    },
    handleTextAlignChange(value) {
      if (!this.component.data.style) {
        this.component.data.style = {}
      }
      this.component.data.style['text-align'] = value
    }
  }
}
</script>

<style scoped>
.style-setting-wrapper{
  margin: 10px;
}
.item-wrapper{
  padding: 0 20px 0px 20px;
  border-bottom: 1px solid #ccc;
}
.item-header{
  line-height: 40px;
}
.item{
  display: flex;
  margin-bottom: 16px;
}
.item-title{
  width: 60px;
  line-height: 32px;
}
.item-input{
  flex: 1;
}
</style>


