<template>
  <div class="wrapper">
    <multi-functional-form v-model="items"/>
    <Divider/>
    <multi-functional-table v-model="items1"/>
    <Divider/>
    <multi-functional-table v-model="items1" :keys="[111,22,333]"/>
   <!--  <Table :columns="columns1" :data="data1"></Table> -->
  </div>
</template>

<script>
import MultiFunctionalForm from '../components/MultiFunctionalForm'
import MultiFunctionalTable from '../components/MultiFunctionalTable'
export default {
  components: {
    MultiFunctionalForm,
    MultiFunctionalTable
  },
  data () {
    return {
      items: [],
      items1: [],
      columns1: [
          {
              title: 'Name',
              key: 'name'
          },
          {
              title: 'Age',
              key: 'age'
          },
          {
              title: 'Address',
              key: 'address'
          }
      ],
      data1: [
          {
              name: 'John Brown',
              age: 18,
              address: 'New York No. 1 Lake Park',
              date: '2016-10-03'
          },
          {
              name: 'Jim Green',
              age: 24,
              address: 'London No. 1 Lake Park',
              date: '2016-10-01'
          },
          {
              name: 'Joe Black',
              age: 30,
              address: 'Sydney No. 1 Lake Park',
              date: '2016-10-02'
          },
          {
              name: 'Jon Snow',
              age: 26,
              address: 'Ottawa No. 2 Lake Park',
              date: '2016-10-04'
          }
      ]
    }
  }
}
</script>

<style scoped>
.wrapper{
  background: white;
}
</style>



