<template>
  <div>
     <Collapse v-model="value1">
        <Panel name="1">
            
            <p slot="content">Steve Jobs1955224</p>
        </Panel>
        <Panel name="2" hideArrow>
            
            <p slot="content">Stephen Gary WozniakUC BerkeleyEECS1987</p>
        </Panel>
        <Panel name="3">
            
            <p slot="content">AppleiPodiMaciPhoneiPad</p>
        </Panel>
    </Collapse>
    <p v-if="false">33434</p>
    <test ref="test" :items="[3,4,5]"/>
    <p @click="test" ref="p">1213click</p>
    <!-- <Card title="abc">
      <p slot="title">The standard card</p>
      <p>Content of card</p>
      <p>Content of card</p>
      <p>Content of card</p>
    </Card> -->
  <Modal v-model="a" title="" width="600">
    <Table :columns="columns" :data='data'></Table>
  </Modal>
  <pre class="test-pre" ref="pre">{{ data }}</pre>
  </div>
</template>

<script>
import test from '@/components/test'
export default {
  components: {
    test
  },
  data () {
    return {
      a: true,
      value1: [1,2],
      columns: [],
      data: [{
        name: 'huashi',
        age: 22,
        ser: 1
      }]
    }
  },

  methods: {
    test(){
      /* this.$refs.test.test() */
      console.log(this.$refs.test, this.$refs.p)
      console.log(this.$refs.pre.innerHTML)
      // this.a = true
      // console.log(this.$children)
      // this.$children[1].parentTrigger(33)
    }
  }
}
</script>

<style scoped>
.test-pre{
  background: #ccc;
  padding: 24px;
  text-align: left;
}
</style>


