export const root = {
  tag: 'div',
  id: 1,
  data: {
    style: {
      width: '100%',
      height: '100%',
      'min-height': '200px',
      border: '1px solid #ccc'
    },
    props: {
      type: 'primary',
      size: 'this.curSize'
    },
    class: 'a b c',
    on: {
      click: () => {console.log(11)}
    }
  },
  children: [
    {
      tag: 'p',
      id: 11,
      text: '111'
    },
    {
      tag: 'h1',
      id: 111,
      text: '333'
    },
    {
      tag: 'div',
      id: 1111,
      children: [{
        tag: 'h2',
        id: 12,
        text: 444
      },{
        tag: 'Table',
        id: 13,
        text: 999
      }]
    }
  ]
}

function _c (node) {
  let oEle = document.createElement(node.tag)
  const data = node.data
  for (let key in data) {
    switch(key) {
      case 'style':
        for (let item in data.style) {
          oEle.style[item] = data.style[item]
        }
        break;
      case 'class':
        oEle.className = data.class
        break;
      case 'props':
        for (let item in data.props) {
          oEle.setAttribute(item, props[item])
        }
        break;
      default:
        // to-do
        break;
    }
  }
}


