<template>
  <div class="wrapper">
    <span class='label'>{{label}}:</span>
    <span class='text'><slot></slot></span>
  </div>
</template>

<script>
export default {
  props: {
    label: {
      type: String,
    },
  },
  data () {
    return {
      
    };
  },

  methods: {
    
  }
}
</script>

<style scoped>
.wrapper{
  padding-bottom: 16px;
}
.label{
  color: rgba(0,0,0,0.85);
  font-weight: normal;
  font-size: 14px;
  line-height: 1.5;
  white-space: nowrap;
}
.text{
  color: rgba(0,0,0,0.65);
  font-size: 14px;
  line-height: 1.5;
}
</style>


