<template>
  <div class="wrapper">
    <div class="back-wrapper" v-if="backIcon">
      <Icon type="md-arrow-back" /><Divider type="vertical"/>
    </div>
    <div style="display: inline-block">
      <span class="title">{{title}}</span>
      <span class="subtitle">{{subTitle}}</span>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    title: {},
    subTitle: {},
    backIcon: {
      type: Boolean,
      default: true
    }
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped>
.wrapper{
  position: relative;
  padding: 16px 24px;
  background: #fff;
}
.back-wrapper{
  display: inline-block;
  padding: 4px 0;
  font-size: 16px;
  line-height: 100%;
  cursor: pointer;
}
.title{
  padding-right: 12px;
  color: rgba(0,0,0,.85);
  font-weight: 700;
  font-size: 16px;
  line-height: 1.4;
}
.subtitle{
  display: inline-block;
  padding-right: 12px;
  color: rgba(0,0,0,.45);
  font-size: 14px;
  line-height: 1.8;
}
</style>



