import { generateUUID } from '../utils/assist'
import localforage from 'localforage'
var pageInstance = localforage.createInstance({
  name: "page"
});
export default class PageApi {
  addPage(params) {
    return pageInstance.setItem(generateUUID(), params)
  }
  deletePage(id) {
    return pageInstance.removeItem(id)
  }
  getPage(id) {
    return pageInstance.getItem(id)
  }
  updatePage(id, page) {
    return pageInstance.setItem(id, page)
  }
  async getAllPages() {
    const list = []
    await pageInstance.iterate((value, key) => {
      // console.log(key, value)
      list.push({
        id: key,
        ...value
      })
    })
    return list
  }
}


