// settingdom
export function setting2domTree(ele, data, setting, tag, count) {
  for (let key in setting) {
    // isSlot
    let item = setting[key]
    if (key === 'text') {
      ele.innerText = item.value
    } else {
      if (item.isSlot) {
        item.value.map(o => {
          let node = document.createElement(item.name)
          for (let i in o) {
            if (i === 'text') {
              node.innerText = o[i]
            } else {
              node.setAttribute(i, o[i])
            }
          }
          ele.appendChild(node)
        })
      }else if (!item.for && item.value) {
        ele.setAttribute(key, item.value);
      }
    }
  }
}



