export default class Stack {
  constructor () {
    this.data = []
  }
  getTop () {
    return this.getLength() === 0 ? null : this.data[this.data.length - 1]
  }
  getLength () {
    return this.data.length
  }
  push (v) {
    this.data.push(v)
  }
  pop () {
    return this.data.pop()
  }
}


