<template>
  <div class="wrapper" id="wrapper">
    <!-- <tree :data="data5" :render="renderContent"></tree> -->
    <input type="text" id="test" ref="test" v-model="aaa" @blur="handleBlur" @copy="test($event)">
    <div class="wrapper" @click="a">
        <div class="test"></div>
    </div>
    <div class="huashi" id="huashi" @click="test2($event)">
        <p class="s" id="s"></p>
    </div>
  </div>
</template>
<script>
import test from '@/components/test'
    export default {
      components: {
        test,
      },
      data () {
          return {
              data5: [
                  {
                      title: 'parent 1',
                      expand: true,
                      render: (h, { root, node, data }) => {
                          return h('span', {
                              style: {
                                  display: 'inline-block',
                                  width: '100%'
                              }
                          }, [
                              h('span', [
                                  h('Icon', {
                                      props: {
                                          type: 'ios-folder-outline'
                                      },
                                      style: {
                                          marginRight: '8px'
                                      }
                                  }),
                                  h('span', data.title)
                              ]),
                              h('span', {
                                  style: {
                                      display: 'inline-block',
                                      float: 'right',
                                      marginRight: '32px'
                                  }
                              }, [
                                  h('Button', {
                                      props: Object.assign({}, this.buttonProps, {
                                          icon: 'ios-add',
                                          type: 'primary'
                                      }),
                                      style: {
                                          width: '64px'
                                      },
                                      on: {
                                          click: () => { this.append(data) }
                                      }
                                  })
                              ])
                          ]);
                      },
                      children: [
                          {
                              title: 'child 1-1',
                              expand: true,
                              children: [
                                  {
                                      title: 'leaf 1-1-1',
                                      expand: true
                                  },
                                  {
                                      title: 'leaf 1-1-2',
                                      expand: true
                                  }
                              ]
                          },
                          {
                              title: 'child 1-2',
                              expand: true,
                              children: [
                                  {
                                      title: 'leaf 1-2-1',
                                      expand: true
                                  },
                                  {
                                      title: 'leaf 1-2-1',
                                      expand: true
                                  }
                              ]
                          }
                      ]
                  }
              ],
              buttonProps: {
                  type: 'default',
                  size: 'small',
              },
              aaa: '2323',
              b: [{
                  a:23,
                  f:34
              }]
          }
      },
      watch: {
          b: {
              deep: true,
              handler(val){
                  console.log(val, 'change')
              }
          }
      },
      mounted(){
        // this.$on('test', function (x) {
        //   console.log(x.repeat(2))
        // })
        this.$refs.test.select()
      },
      methods: {
        a(){
          this.b[0] = {}
          console.log('aaa')
        },
        handleBlur () {
            this.b.push({a:121})
            console.log(this.aaa)
        },
        renderContent (h, { root, node, data }) {
            return h('span', {
                style: {
                    display: 'inline-block',
                    width: '100%'
                }
            }, [
                h('span', [
                    h('Icon', {
                        props: {
                            type: 'ios-paper-outline'
                        },
                        style: {
                            marginRight: '8px'
                        }
                    }),
                    h('span', data.title)
                ]),
                h('span', {
                    style: {
                        display: 'inline-block',
                        float: 'right',
                        marginRight: '32px'
                    }
                }, [
                    h('Button', {
                        props: Object.assign({}, this.buttonProps, {
                            icon: 'ios-add'
                        }),
                        style: {
                            marginRight: '8px'
                        },
                        on: {
                            click: () => { this.append(data) }
                        }
                    }),
                    h('Button', {
                        props: Object.assign({}, this.buttonProps, {
                            icon: 'ios-remove'
                        }),
                        on: {
                            click: () => { this.remove(root, node, data) }
                        }
                    })
                ])
            ]);
        },
        append (data) {
            const children = data.children || [];
            children.push({
                title: 'appended node',
                expand: true
            });
            this.$set(data, 'children', children);
        },
        remove (root, node, data) {
            const parentKey = root.find(el => el === node).parent;
            const parent = root.find(el => el.nodeKey === parentKey).node;
            const index = parent.children.indexOf(data);
            parent.children.splice(index, 1);
        },
        test(e){
            console.log(e,e.clipboardData.getData('text/plain'))
        },
        test2(e){
            console.log(e)
        }
      }
    }
</script>
<style scoped>
.wrapper{
    position: relative;
    width: 300px;
    height: 300px;
    background: rgb(253, 230, 230);
}
.test{
    display: inline-block;
    width: 0px;
    position: absolute;
    top: 20px;
    left: 20px;
    right: 20px;
    bottom: 20px;
    background: #ccc;
}
#wrapper p{
  color: red;
}
#s{
    color: rgb(68, 75, 180);
}
</style>


