/*
 * @Author: hch
 * @Date: 2019-03-18 15:39:11
 * @info: ''
 */
<template>
  <div class="multi-functional-table">

    <div v-show="bulkMode" class="bulk">
      <div class="bulk-first-line"></div>
      <div class="bulk-content">
        <Input type="textarea" :rows="10" v-model="bulkStr" @on-change="handleBulkStrChange"></Input>
      </div>
    </div>

    <table v-show="!bulkMode">
      <thead>
        <tr class="thead-tr">
          <th class="first-column"></th>
          <th class="content-column">KEY</th>
          <th class="content-column">VALUE</th>
          <th class="content-column">DESCRIPTION</th>
        </tr>
      </thead>
      <tbody ref='tbody'>
        <tr
          v-for="(item, index) in items"
          :key="index"
          @mouseout="hideTip(index)"
          :class="{selectedTr: item['isMouseOver']}"
          @dragover="handleDragover($event)"
          @dragstart="startDrag($event, index)"
          @drop="handleDrop($event, index)"
          :ref="'item' + index">
          <td>
            <div class="first-td">
              <Icon
                v-if="index !== items.length - 1"
                type="ios-menu"
                size="16"
                class="change-position-icon"
                @mouseover.native="handleCIconMouseOver(index)"
                @mouseout.native="handleCIconMouseOut(index)"/>
              <Checkbox v-model="item.isSelected" class="item-checkbox"/>
            </div>
          </td>
          <td>
            <input
              type="text"
              v-model="item.key"
              placeholder="Key"
              @change="handleInputChange(index, 'key')">
          </td>
          <td>
            <input
              type="text"
              v-model="item.value"
              placeholder="Value"
              @change="handleInputChange(index, 'value')">
          </td>
          <td class="last-td">
            <input
              type="text"
              v-model="item.description"
              placeholder="Description"
              @change="handleInputChange(index, 'description')">
            <Icon
              v-if="index !== items.length -1"
              type="ios-close"
              class="close-icon"
              size="16"
              @mouseover='handleIconMouseOver(index)'
              @click='handleItemDelete(index)'/>
          </td>
        </tr>
      </tbody>
    </table>

    <div class="edit-mode">
      <span @click="toggleEditMode($event)" id="bulk" name="bulk">Bulk Edit</span>
    </div>
  </div>
</template>

<script>
/* const TAG = 'MultiFunctionalTable' */
export default {
  components: {
  },
  props: {
    // tableWidth: {
    //   type: Number,
    //   default: 1000
    // }
    value: {
      type: Array,
      required: true,
      validator: (val) => {
        if (!Array.isArray(val)) {
          return false
        }
        return true
      }
    },
    flag: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      items: this.value2items(this.value),
      curDragIdx: null,
      bulkMode: false,
      bulkStr: ''
    }
  },
  watch: {
    bulkMode (val) {
      if (val === true) {
        this.bulkStr = this.items2bulkStr(this.items)
      } else {
        this.items = this.bulkStr2items(this.bulkStr)
      }
    },
    // watch deep ?
    value (val) {
      this.items = this.value2items(val)
    }
  },
  methods: {
    createFunctionalTr (isSelected = false, key = '', value = '') {
      return {
        isMouseOver: false,
        isSelected: isSelected,
        key: key,
        value: value,
        description: ''
      }
    },
    hideTip (index) {
      // this.items[index]['isMouseOver'] = false
    },
    handleIconMouseOver (index) {
      this.items[index]['isMouseOver'] = true
      console.log(111)
    },
    // 
    handleItemDelete (index) {
      this.items.splice(index, 1)
      this.$emit('input', this.items2value(this.items))
    },
    // 
    handleInputChange (index, key) {
      // this.$emit('my-event', this.flag)
      let temp = ['key', 'value', 'description']
      temp = temp.filter(function (item) {
        if (item !== key) {
          return item
        }
      })
      if (this.items[index][temp[0]] === '' && this.items[index][temp[1]] === '') {
        if (!this.items[index + 1]) {
          this.items.push(this.createFunctionalTr())
        }
      }
      this.items[index].isSelected = true
      this.$emit('input', this.items2value(this.items))
    },
    startDrag (e, index) {
      // console.log('drag', e.target, index)
      this.curDragIdx = index
    },
    handleDrop (e, idx) {
      // console.log('drop', e.target, idx)
      if (idx === this.items.length - 1) {
        return
      }
      if (this.curDragIdx !== idx) {
        // console.log(this.curDragIdx, idx)
        let temp = this.items[this.curDragIdx]
        this.items.splice(this.curDragIdx, 1, this.items[idx])
        this.items.splice(idx, 1, temp)
      }
      // console.log(this.items)
    },
    handleDragover (e) {
      // console.log('dragover')
      e.preventDefault()
    },
    // icon
    handleCIconMouseOver (index) {
      this.$refs['tbody'].childNodes[index].draggable = true
    },
    handleCIconMouseOut (index) {
      this.$refs['tbody'].childNodes[index].draggable = false
    },
    // 
    toggleEditMode (e) {
      let ele = e.target || e.srcElement
      if (ele.id === 'bulk') {
        this.bulkMode = true
        ele.id = 'keyValue'
        ele.innerText = 'Key-Value Edit'
      } else {
        this.bulkMode = false
        ele.id = 'bulk'
        ele.innerText = 'Bulk Edit'
      }
    },
    // valueitems
    value2items () {
      let temp = this.value.map(o => {
        o.isMouseOver = false
        if (o.isSelected !== false) {
          o.isSelected = true
        }
        return o
      })
      temp.push({
        isMouseOver: false,
        isSelected: false,
        key: '',
        value: '',
        description: ''
      })
      return temp
    },
    items2value () {
      let newValue = []
      for (let i = 0; i < this.items.length - 1; i++) {
        let { isSelected, key, value, description } = this.items[i]
        let obj = {
          isSelected,
          key,
          value,
          description
        }
        newValue.push(obj)
      }
      return newValue
    },
    // itemsbulkStr
    items2bulkStr (items) {
      let temp = []
      for (let i = 0; i < items.length - 1; i++) {
        let { isSelected, key, value } = items[i]
        temp.push(isSelected ? `${key}:${value}` : `//${key}:${value}`)
      }
      return temp.join('\n')
    },
    bulkStr2items (bulkStr) {
      if (!bulkStr.endsWith('\n')) {
        bulkStr += '\n'
      }
      let items = []
      let _temp = bulkStr.split('\n')
      _temp = _temp.map(o => {
        return o.trim()
      })
      for (let i = 0; i < _temp.length - 1; i++) {
        let o = _temp[i]
        if (o) {
          let str = o.startsWith('//') ? o.substring(2) : o
          let arr = str.split(':')
          let item = this.createFunctionalTr(true, arr[0], arr[1] || '')
          if (arr.length > 2) {
            arr.shift()
            item.value = arr.join(':')
          }
          if (o.startsWith('//')) {
            item.isSelected = false
          }
          items.push(item)
        }
      }
      items.push(this.createFunctionalTr())
      return items
    },
    // bulk edittab(bugtab) watch
    handleBulkStrChange: _.debounce(function () {
      this.items = this.bulkStr2items(this.bulkStr)
      this.$emit('input', this.items2value(this.items))
    }, 200)
  }
}
</script>

<style scoped>
.multi-functional-table{
  width: 100%;
  position: relative;
}
/* bulk */
.bulk{
  border: 1px solid #ccc;
}
.bulk-first-line{
  height: 31px;
  border-bottom: 1px solid #ccc;
}
.bulk-content{
  min-height: 200px;
}
table{
  width: 100%;
  border-collapse: collapse;
  position: relative;
}
.thead-tr{
  position: relative;
}
.edit-mode{
  line-height: 30px;
  position: absolute;
  right: 10px;
  top: 0px;
  color: #F3784B;
}
.edit-mode:before{
  content: '|';
  color: #ccc;
  margin-right: 10px;
}
tr{
  position: relative;
}
.first-td{
  position: relative;
  height: 100%;
}
.last-td{
  position: relative;
}
td,th{
  border: 1px solid#EAEAEA;
  height: 30px;
  line-height: 30px;
  text-align: left;
  padding-left: 5px;
}
input{
  border: none;
  height: 30px;
  line-height: 30px;
  width: 100%;
}
.first-column{
  width: 50px;
  text-align: right;
}
.hidden-column{
  display: none;
}
.content-column{
  min-width: 200px;
}
.close-icon{
  position: absolute;
  right: 5px;
  top: 5px;
}
.change-position-icon{
  cursor: s-resize;
}
.item-checkbox{
  position: absolute;
  right: 5px;
  top: 0;
}
</style>



