import axios from 'axios'
export function getDataTranslation (data) {
  console.log(data, 'api/translation')
  return axios.post('/trans/data', {
    data
  })
}

export function getEsonArrayTranslation (data) {
  // console.log(data, 'api/eson/translation')
  /* if (window.env.flag) {
    return axios.post(window.env.host + '/trans/eson', {
      data
    })
  } */
  return axios.post('/trans/eson', {
    data
  })
}

export function getObjTranslation (data) {
  // console.log(data, 'api/translation')
  return axios.post('/trans/obj', {
    data
  })
}


