<template>
  <div>
    <Button type='primary' @click="() => {this.showExampleModal = true}"></Button>
    <div style="display:flex;margin-top: 20px">
      <div class="eson-editor-wrapper" ref="eson-editor" contenteditable="true"></div>
    </div>
    <Modal
      v-model="showExampleModal"
      title="">
      <p></p>
      <div style="display:flex">
        <Input type="textarea" disabled :placeholder="placeholder" :rows="10" class="example-input"/>
        <pre class="example-pre">{{ exampleCode }}</pre>
      </div>
      <div slot="footer">
        <Button @click="() => { this.showExampleModal = false}" type="primary" class="button"></Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import { esonText2arr, validStructrue, arr2json, eson2json } from '../tools/eson'
import { validStructure2 } from '../tools/objCollection'
export default {
  components: {
    
  },
  props: {
    array: {
    },
    tip: {
      type: String,
      default: 'key:value'
    }
  },
  model: {
    prop: 'array'
  },
  data () {
    return {
      showExampleModal: false,
      code: [],
      placeholder: `
        key:title
        a:b
        d:e
      `,
      exampleCode: [
        {
          "key": "a",
          "title": "b"
        },
        {
          "key": "d",
          "title": "e"
        }
      ]
    };
  },
  watch: {
    array(val) {
      if (val.length === 0) {
        this.$refs['eson-editor'].innerText = this.tip
      } else {
        let str = this.arr2str(this.array)
        this.$refs['eson-editor'].innerText = str
      }
    }
  },
  methods: {
    handleSwitch(){
      let str = this.$refs['eson-editor'].innerText
      let { esonArr, iMixMode } = esonText2arr(str)
      console.log(esonArr, 'esonArr')
      try {
        let collection = validStructure2(esonArr)
        console.log(collection)
        this.code = collection
        this.$emit('input', this.code)
      } catch (e) {
        console.log(e, 'createCollection fail')
        this.$Message.error(e.message)
      }
    },
    getCode () {
      let str = this.$refs['eson-editor'].innerText
      let { esonArr, iMixMode } = esonText2arr(str)
      console.log(esonArr, 'esonArr')
      try {
        let collection = validStructure2(esonArr)
        return collection
      } catch (e) {
        console.log(e, 'fail')
        this.$Message.error(e.message)
      }
    },
    // array -> editor innerText
    arr2str(arr){
      // bug 12.6:key
      let str = ''
      if (!Array.isArray(arr) || arr.length === 0) {
        return str
      }
      let keys = this.gatherAllKey(arr)
      let str_key = keys.join(':')
      str += str_key + '\n'
      arr.forEach(item => {
        let _str = ''
        keys.forEach(key => {
          _str += (item[key] ? item[key] : '') + ':'
          //_str += item[key] + ':'
        })
        _str = _str.substr(0, _str.length - 1)
        str += _str + '\n'
      })
      return str
    },
    gatherAllKey(collection) {
      let obj = {}
      collection.map(item => {
        for (let i in item) {
          obj[i] = true
        }
      })
      return Object.keys(obj)
    }
  }
}
</script>

<style scoped>
.eson-editor-wrapper{
  flex: 1;
  border: 1px solid #ccc;
  height: 300px;
  text-align: left;
  color: rgb(206, 145, 120);
  background: #1E1E1E;
  font-size: 14px;
  margin-bottom: 10px;
}
.example-pre{
  background: #333;
  color: white;
  flex: 1;
  margin-left: 20px;
  padding: 20px;
  min-height: 300px;
  vertical-align: top;
}
.example-input{
  margin: 1em 0;
  width: 200px;
}
</style>


