export default class EsonEditor{
  defaultHtml = `<div class="eson-editor-row"></div>`
  constructor (ele) {
    this.ele = ele
    this.init()
    this.ele.onkeyup = (e) => {
      e.preventDefault()
      this.handleKeyBoardEvent(e, this)
    }
  }
  init() {
    if (this.ele.innerHTML === '') {
      this.ele.innerHTML = this.defaultHtml
    }
  }
  handleKeyBoardEvent(e) {
    let code = e.keyCode
    const commands = {
      8: 'delete',
      13: 'enter'
    }
    if (code in commands) {
      this[commands[code]](e)
    }
  }
  delete (e) {
    console.log('delete')
    if (this.ele.innerHTML === '') {
      this.ele.innerHTML = this.defaultHtml
    }
  }
  enter(e) {
    console.log('enter')
    this.ele.innerHTML += this.defaultHtml
  }
}


