<template>
  <div>
    <div v-html="html"></div>
    <!-- <div>{{html}}</div> -->
    <div ref='code' :style="{ height:'100px',background: '#ccc'}">222</div>
    <div id="gg" @click='test'><h1>test11111111</h1></div>
    <Render/>
  </div>
</template>

<script>
import Render from '../base/Render'
import {root} from '../consts/node'
export default {
  components: {
    Render
  },
  data () {
    return {
      html: null,
      root: root,
    };
  },
  mounted(){
    let obj = document.createElement('div')
    let gg =document.getElementById('gg')
    this.html = this.render(this.root)
    let str = this.html.outerHTML
    /* console.log(this.html, str, typeof str, str.indexOf('h1')) */
    //str = str.replace(/"/g, '')
    /* console.log(this.html, str) */
    this.$refs.code.textContent = str
    //document.documentElement.innerHTML = '<h2>22</h2>'
  },
  methods: {
    render(node) {
      let nodeEle = document.createElement(node.tag);
      let props = node.data && node.data.props
      if (props) {
        for (let key in props) {
          if (props[key].indexOf('this') !== -1) {
            nodeEle.setAttribute(key, `{${props[key]}}`)
          } else {
            nodeEle.setAttribute(key, props[key])
          }
        }
      }
      let events = node.data && node.data.on
      if (events) {
        for (let key in events) {
          nodeEle.setAttribute(`on${key}`, events[key])
        }
      }
      for (let item of node.children) {
        let childEle
        if (item.text) {
          childEle = document.createElement(item.tag)
          childEle.innerText = item.text
          nodeEle.appendChild(childEle)
        } else {
          childEle = this.render(item)
        }
        nodeEle.appendChild(childEle)
      }
      return nodeEle
    },
    sleep(time){
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          resolve()
        }, time)
      })
    },
    test() {
      console.log(11, )
    }
  }
}
</script>

<style scoped>

</style>


