import { LOCALE_FLAG } from '../../consts/LocaleFlag'
/*
 *@method 
 *@for 
 *@param{obj}  {title: '', key: 'df', slot: ''}
 *@return {obj} {key: 'df', value: '}
*/
export function getCollectionItemLocale(obj) {
  const REG_ZH = /[\u4e00-\u9fa5]/
  let res = {
    key: '',
    value: ''
  }
  let zh_key = ''
  for (let key in obj) {
    const value = obj[key]
    if (REG_ZH.test(value)) {
      zh_key = key
      res.value = value
      for (let i in obj) {
        if (i !== key && /[a-zA-Z]/.test(obj[i])) {
          res.key = obj[i]
          break
        }
      }
      if (!res.key) {
        res.key = key
      }
      obj[key] = `${LOCALE_FLAG}${res.key}`
      break
    }
  }
  return {
    zh_key,
    res
  }
}


