const inlineComponents = ['Col', 'Button', 'Divider', 'a']
import { renderButton } from './renderButton'
const noChidlrenComponents = [
  'a',
  'Button',
  'Divider',
  'Page',
]
// 
export function renderAssist(h, ctx, node) {
  const activeId = ctx.props.activeId
  if (activeId != node.id){
    return
  }
  // tag
  const tagStyle = {
    position: 'absolute',
    top: '-24px',
    left: 0,
    'z-index': 1
  }
  const Tag = h('Tag', {
    props: {color: 'primary'},
    style: tagStyle
  }, node.tag)
  // Add Button
  const addButtonStyle = {
    position: 'absolute',
    bottom: '-12px',
    left: '50%',
    'z-index': 1,
    transform: 'translate(-50%, 0)'
  }
  const addButtonProps = { icon: 'md-add', shape: 'circle'}
  const addButton = renderButton(h, ctx, addButtonProps, addButtonStyle, 'add')

  // Operation buttons
  const upButton = renderButton(h, ctx, {icon: 'md-arrow-up'}, {}, 'up')
  const downButton = renderButton(h, ctx, {icon: 'md-arrow-down'}, {}, 'down')
  const parentButton = renderButton(h, ctx, {icon: 'ios-git-branch'}, {}, 'parent')
  const copyButton = renderButton(h, ctx, { icon: 'ios-copy'}, {}, 'copy')
  const deleteButton = renderButton(h, ctx, { icon: 'ios-trash'}, {}, 'delete')
  const clearChildrenButton = renderButton(h, ctx, {icon: 'md-log-out'}, {}, 'clear')

  const operationButtons = [parentButton, upButton, downButton, copyButton, deleteButton];
  if (noChidlrenComponents.indexOf(node.tag) === -1) {
    operationButtons.push(clearChildrenButton)
  }
  let _style = {}
  if (inlineComponents.indexOf(node.tag) > -1) {
    _style = {
      position: 'absolute',
      top: '0px',
      left: '30px',
      'z-index': 1,
    }
  } else {
    _style = {
      position: 'absolute',
      top: '-24px',
      right: 0,
      'z-index': 1
    }
  }
  const operationButtonGroup = h('ButtonGroup', {
    props: {
      /* vertical: node.tag === 'Button' ? true : false, */
      ...(inlineComponents.indexOf(node.tag) > -1 ? {} : {
        shape: 'circle'
      })
    },
    style: _style,
  }, operationButtons)
  if (activeId === 1) {
    return [Tag, addButton]
  } else {
    return [Tag, addButton, operationButtonGroup]
  }
}


