<script>
import { typeOf } from '../utils/assist'
export default {
  props: {
    jdata: {
      type: Object
    }
  },
  data() {
    return {
      close_folder_img: require('../assets/closefolder.png'),
      open_folder_img: require('../assets/openfolder.png'),
      node_img: require('../assets/node.png'),
      node_last_img: require('../assets/lastNode.png'),
      line_img: require('../assets/line.png')
    }
  },
  render(){
    let that = this
    function objRender(json){
      let aEle = []
      for(let i in json){
        let item = getObjectDom(i, json[i])
        aEle.push(item)
      }
      return <ul>{aEle}</ul>
    }
    function arrRender(arr){

    }
    function getArrayDom(arr) {
      return arr.map((o, index) => {
        if(typeOf(o) === 'object'){
          return getObjectDom(index, o)
        }else if(typeOf(o) === 'array'){
          return getObjectDom(index, o)
        }else{
          return <li class="elemBox">
            <div class="row">
              <span class="json-value">{o}</span>
            </div>
          </li>
        }
      })
    }
    function getObjectDom (key, value) {
      if(typeOf(value) === 'array'){          // 
        return <li class="elemBox">
          <div class="row folder-name">
            <a href="javascript:void(0)" onClick={($event) => that.handleNodeImgClick($event)} class="icon-wrapper">
              <img
                src={that.close_folder_img}
                alt="close"
                class="icon"/>
            </a>
            <span class="json-key" onClick={($event) => that.test($event)}>
              {key}
            </span>
            <span class="array-length">{value.length}</span>
          </div>
          <ul class="array">
            
           {getArrayDom(value)}
            
          </ul>
        </li>
      }else if(typeOf(value) === 'object'){  // 
        return <li class="elemBox">
            <div class="row folder-name">
              <a href="javascript:void(0)" onClick={($event) => that.handleNodeImgClick($event)} class="icon-wrapper">
                <img
                  src={that.close_folder_img}
                  alt="close"
                  class="icon"/>
              </a>
              <span class="json-key" onClick={($event) => that.test($event)}>
                {key}
              </span>
            </div>
            <div class="object folder-content">{objRender(value)}</div>
        </li>
      }else{                                // 
        return <li class="elemBox">
          <div class="row">
            <span class="json-key">
              {key}
            </span>
            <span class="json-value">{value}</span>
          </div>
        </li>
      }
    }
    return <div class="wrapper">
      <div class="top-json">
        <a href="javascript:void(0)" onClick={($event) => that.handleImgClick($event)} class="icon-wrapper">
          <img
            src={this.close_folder_img}
            alt="close"
            class="icon"
            title="close"/>
        </a>
        <span class="">JSON</span>
      </div>
      <div class="json-content" ref="jsonContent">{objRender(this.jdata)}</div>
    </div>
  },
  methods: {
    test(e){
      console.log(23,e)
      let oEle = e.target.parentNode.parentNode.children[1]
      if(oEle.style.display === 'none'){
        oEle.style.display = 'block'
      }else{
        oEle.style.display = 'none'
      }
    },
    handleImgClick (e) {
      console.log(e,55)
      if(e.target.src === this.open_folder_img){ // alt?
        e.target.src = this.close_folder_img
        e.target.alt = 'close'
        this.$refs.jsonContent.style.display = 'block';
      }else{
        e.target.src = this.open_folder_img
        e.target.alt = 'open'
        this.$refs.jsonContent.style.display = 'none';
      }
    },
    handleNodeImgClick(e) {
      if(e.target.src === this.open_folder_img){ // alt?
        e.target.src = this.close_folder_img
        e.target.alt = 'close'
      }else{
        e.target.src = this.open_folder_img
        e.target.alt = 'open'
      }
      let oEle = e.target.parentNode.parentNode.parentNode.children[1]
      if(oEle.style.display === 'none'){
        oEle.style.display = 'block'
      }else{
        oEle.style.display = 'none'
      }
    }
  },
}
</script>

<style scoped>
li{
  list-style: none;
}
.wrapper{
  font-size: 14px;
  background: #FAFAFF;
}
.elemBox{
  background-image: url(../assets/line.png);
  background-repeat: repeat-y;
  background-size: 16px 22px;
  line-height: 24px;
  list-style: none;
  vertical-align: top;
  cursor: row-resize;
}
.row{
  background-image: url(../assets/node.png);
  background-repeat: no-repeat;
  background-size: 16px 22px;
  padding: 0 0 0 16px;
  cursor: pointer;
  height: 24px;
}
.row:hover{
  font-weight: bold;
}
.top-json{
  height:24px;
}
.top-json>span{
  display: inline-block;
  height: 100%;
}
.json-content{
  padding: 0 0 0 16px;
}
.json-key{
  position:relative;
  color: #4A0;
  padding: 3px 8px;
  font-weight: bold;
}
.json-key:after{
  content: ' :';
  color: #2c3;
  font-weight: bold;
}
.json-value{
  color: #CC00FF;
  padding: 0 4px 0 8px;
  display: inline-block;
}
.array{
  display: block;
  padding-left: 22px;
}
/* .array-value{
  color: #f63;
} */
.array-comma:after{
  content: ',';
  color: #c0f;
}
.array-length {
  display: inline;
  padding: 0 10px;
  margin: 0 0 0 4px;
  height: 20px;
  line-height: 20px;
  background-color: #d3d3d3;
  border-radius: 4px;
  text-shadow: none;
  color: #fff;
}
/* .array-last-child{
  background-image: url(../assets/lastNode.png);
  background-repeat: repeat-y;
  background-size: 16px 22px;
} */
.object{
  display: block;
  padding-left: 22px;
}

.json-content>ul>.elemBox:last-child{
  background-image: none;
}
/* .object>ul>.elemBox>li>row{
  background-image: url(../assets/lastNode.png);
  background-repeat: repeat-y;
  background-size: 16px 22px;
} */
.object>ul>.elemBox:last-child{
  background-image: none;
}
.array>.elemBox:last-child{
  background-image: none;
}
.folder-name{
  padding: 0;
}
ul>li:last-child>.row{
  background-image: url(../assets/lastNode.png);
  background-repeat: repeat-y;
  background-size: 16px 22px;
}
.icon-wrapper{
  display: inline-block;
  width: 16px;
  height: 22px;
}
.icon{
  display: inline-block;
  width: 16px;
  height: 22px;
  cursor: row-resize;
}
</style>



