import { LOCALE_FLAG } from "../../consts/LocaleFlag"

// 

export function processTemplateLanguage(str) {
  const re_prop = new RegExp(`"${LOCALE_FLAG}([.a-zA-Z1-9]+)"`, 'g')
  str = str.replace(re_prop, (all, $1) => {
    return `{this.$l('${$1}')}`
  })
  const re_text = new RegExp(`${LOCALE_FLAG}([.a-zA-Z1-9]+)`, 'g')
  str = str.replace(re_text, (all, $1) => {
    return `{this.$l('${$1}')}`
  })
  return str
}


export function processDataLanguage(str) {
  const re_value = new RegExp(`"${LOCALE_FLAG}([.a-zA-Z1-9]+)"`, 'g')
  str = str.replace(re_value, (all, $1) => {
    return `this.$l('${$1}')`
  })
  return str
}


