<template>
  <div class="lan-wrapper">
    <div class="lan-left">
      <div class="left-header" @click="handleNewPage">
        <Icon type="md-add" style="margin-right: 5px"/>New Page
      </div>
      <ul v-if="pages.length > 0" @selectstart.prevent>
        <li
          v-for="(item, index) in pages"
          :key="index"
          v-contextmenu:contextmenu
          @contextmenu="test(item)"
          :class="{'page-item': 'true', 'active-page-item': curPage.id === item.id ? true : false }"
          @click="handleItemClick(item)"
          :title="item.name">
          {{ item.name }}
        </li>
        <v-contextmenu ref="contextmenu" @contextmenu="handleContextmenu">
          <v-contextmenu-item></v-contextmenu-item>
          <v-contextmenu-item @click="handlePageDelete"></v-contextmenu-item>
        </v-contextmenu>
      </ul>
      <div style="margin-top: 40px" v-else>
        no page
        <p style="color: #FD783B" @click="handleNewPage">Click here to add page!</p>
      </div>
    </div>
    <div class="lan-middle">
      <div class="lan-middle-nocontent" v-if="curPage === null">Choose or Select a Page!</div>
      <div class="lan-middle-content" v-else>
        <div class="root-wrapper">
          <div class="button-wrapper">
            <Button @click="showExample" type="primary" class="button"></Button>
            <Button @click="clear" type="primary" class="button"></Button>
            <Button @click="save" type="primary" class="button"></Button>
            <Button type="primary" @click="createCollection" v-if="curMode === 'objCollection'"></Button>
            <template v-if="curMode === 'json'">
              <Button @click="set" type="primary" class="button"></Button>
              <Button @click="change2json" type="primary" class="button">JSON</Button>
              <Button @click="multi" type="primary" class="button" :loading="multiLoading"></Button>
              <Button type="primary" @click="switch1" :loading="multiLoading2">()</Button>
            </template>
          </div>
          <div class="eson-mode">
            <Select v-model="curMode">
              <Option v-for="item in modes" :value="item.value" :key="item.value">{{ item.label }}</Option>
            </Select>
          </div>
          <div class="eson-editor-wrapper" ref="eson-editor" contenteditable="true"></div>
        </div>
        <!-- <eson-editor @show-code="showCode"></eson-editor> -->
        <!-- <div contenteditable="true" class="eson-editor-wrapper" ref="eson-editor" placeholder="enter something..."></div> -->
        <!-- <div contenteditable="true" class="edit-wrapper" ref="content"></div> -->
      </div>
    </div>
    <div class="lan-right">
      <div class="code-block">
        <code-block :code="code"/>
      </div>
    </div>
    <Modal
      v-model="newPageModal"
      title=""
      :loading="newPageLoding"
      @on-ok="handlePageNew">
      <Input v-model="pageName" style="margin-top: 5px"></Input>
    </Modal>
    <Modal
      v-model="deletePageModal"
      title=""
      @on-ok="deletePage">
      <span v-if="curContextMenuPage">{{ curContextMenuPage.name }}</span>?
    </Modal>
    <Modal
      v-model="showExampleModal"
      title="">
      <div style="display:flex">
        <Input type="textarea" disabled :placeholder="placeholder" :rows="10" class="example-input"/>
        <pre class="example-pre">{{ exampleCode }}</pre>
      </div>
      <!-- <Split v-model="split1">
        <Input type="textarea" disabled :placeholder="placeholder" :rows="10" class="example-input" slot="left"/>
        <pre class="example-pre" slot="right">{{ exampleCode }}</pre>
      </Split> -->
      <div slot="footer">
        <Button @click="() => { this.showExampleModal = false}" type="primary" class="button"></Button>
      </div>
    </Modal>
    <Modal title="" v-model="settingModal" width="400" @on-ok="saveSetting">
      <Form :label-width="120">
        <FormItem label="key">
          <Input v-model="key_setting.zh" placeholder="key" style="width: 200px"/>
        </FormItem>
        <FormItem label="key">
          <Input v-model="key_setting.en" placeholder="key" style="width: 200px"/>
        </FormItem>
      </Form>
    </Modal>
  </div>
</template>

<script>
  import { getEsonArrayTranslation } from '@/apis/translate'
  import codeBlock from '@/base/codeBlock'
  import EsonEditor from '@/base/EsonEditor'
  import { generateUUID, downloadAsJson } from '../utils/assist'
  import { esonText2arr, validStructrue, arr2json, eson2json } from '../tools/eson'
  import { validStructure2 } from '../tools/objCollection'
  export default {
    components: {
      codeBlock,
      EsonEditor
    },
    data () {
      return {
        curMode: 'json',
        modes: [
          {
            label: 'JSON',
            value: 'json'
          },
          {
            label: '',
            value: 'objCollection'
          }
        ],
        xx: 'remote',
        pages: [],
        curContextMenuPage: null,
        curPage: null,
        newPageModal: false,
        deletePageModal: false,
        newPageLoding: true,
        pageName: '',
        multiLoading: false,
        multiLoading2: false,
        code: '',
        content: '',
        placeholder: `
          #app
          ##header
          a:
          b:
          ##body
          ###sidebar
          title: 
          xx: 
        `,
        exampleCode: {
          app: {
            header: {
              a: '',
              b: ''
            },
            body: {
              sidebar: {
                title: '',
                xx: ''
              }
            }
          }
        },
        showExampleModal: false,
        key_setting: {
          zh: 'zh',
          en: 'en'
        },
        settingModal: false
      }
    },
    created () {
      let pages = localStorage.getItem('jpages')
      if (pages) {
        this.pages = JSON.parse(pages)
        this.curPage = this.pages[0]
        this.$nextTick(() => {
          this.$refs['eson-editor'].innerText = this.curPage.data
        })
      }
      console.log(this.pages, 'created', this.curPage, this.$data, this.$options)
    },
    mounted() {
      let str = localStorage.getItem('setting')
      if (!str) {
        this.key_setting = {
          zh: 'zh',
          en: 'en'
        }
        localStorage.setItem('setting', JSON.stringify(this.key_setting))
      } else {
        this.key_setting = JSON.parse(str)
      }
      
      console.log(localStorage.getItem('setting'))
    },
    methods: {
      handleItemClick (item) {
        this.curPage = item
        this.code = ''
        this.$refs['eson-editor'].innerText = this.curPage.data
        // this.$refs['eson-editor'].innerText = ''
      },
      handleNewPage () {
        this.newPageModal = true
      },
      handlePageDelete () {
        if (!this.curContextMenuPage) {
          return
        }
        console.log(this.curContextMenuPage)
        this.deletePageModal = true
      },
      // new page
      handlePageNew () {
        if(this.pageName.trim() === ''){
          this.$Message.error('')
          this.newPageLoding = false
          this.$nextTick(() => {
            this.newPageLoding = true
          })
        } else {
          this.$Message.success('')
          this.pages.push({
            name: this.pageName,
            data: '',
            id: generateUUID()
          })
          this.pageName = ''
          this.curPage = this.pages[this.pages.length - 1]
          localStorage.setItem('jpages', JSON.stringify(this.pages))
          this.newPageModal = false
        }
      },
      deletePage () {
        for (let i = 0; i < this.pages.length; i++) {
          if (this.pages[i].id === this.curContextMenuPage.id) {
            this.pages.splice(i, 1)
            console.log(this.pages, 'deleted')
            this.curPage = this.pages.length > 0 ? this.pages[0] : null
            this.$Message.success('')
            return
          }
        }
      },
      // 
      clear () {
        this.$refs['eson-editor'].innerText = ''
      },
      set () {
        this.settingModal = true
      },
      save () {
        let obj = {
          name: this.curPage.name,
          data: this.$refs['eson-editor'].innerText,
          id: this.curPage.id
        }
        console.log(obj, localStorage)
        for ( let i = 0; i < this.pages.length; i++) {
          if ( this.pages[i].id === obj.id ) {
            this.pages[i] = obj
            break
          }
        }
        localStorage.setItem('jpages', JSON.stringify(this.pages))
        this.$Message.success('')
      },
      test (item) {
        this.curContextMenuPage = item
        console.log(item.id, 'context-menu')
      },
      saveSetting () {
        if (!this.key_setting.zh || !this.key_setting.en) {
          this.$Message.error('key cannot be empty')
          return
        }
        localStorage.setItem('setting', JSON.stringify(this.key_setting))
      },
      download () {
        console.log(this.code)
        // this.$refs.xxx.innerHTML = JSON.stringify(this.code)
        downloadAsJson(this.code, 'haha')
      },
      handleContextmenu (vnode) {
        console.log(' vnode:', vnode)
      },
      showExample () {
        this.showExampleModal = true
      },
      showCode (code) {
        console.log(code)
        this.code = code
      },
      change2json () {
        let str = this.$refs['eson-editor'].innerText
        console.log(str)
        let json = {}
        let { esonArr, iMixMode } = esonText2arr(str)
        console.log(esonArr, 'esonArr')
        try {
          let json = eson2json(esonArr, iMixMode)
          console.log(json, 'json')
          this.code = json
        } catch (e) {
          console.log(e,'-------')
          this.$Message.error(e.message)
        }
      },
      // 
      async switch1 () {
        /* let flag = window.env.flag
        window.env.flag = !flag
        console.log(window.env.flag)
        this.xx = window.env.flag ? 'remote' : 'locale' */
        let str = this.$refs['eson-editor'].innerText
        let { esonArr, iMixMode } = esonText2arr(str)
        console.log(esonArr)
        try {
          let json = eson2json(esonArr, iMixMode)
          this.multiLoading2 = true
          let res = await getEsonArrayTranslation(esonArr)
          console.log(res.data.data, 'after translation')
          let eng_esonArr = res.data.data
          let eng_json = eson2json(eng_esonArr, iMixMode)
          this.key_setting = JSON.parse(localStorage.getItem('setting'))
          this.code = {
            [this.key_setting.zh]: json,
            [this.key_setting.en]: eng_json
          }
        } catch (e) {
          console.log(e,'-------')
          this.$Message.error(e.message)
        } finally {
          this.multiLoading2 = false
        }
      },
      async multi () {
        let str = this.$refs['eson-editor'].innerText
        let json = {}
        let { esonArr, iMixMode } = esonText2arr(str)
        console.log(esonArr)
        try {
          validStructrue(esonArr, iMixMode)
          this.multiLoading = true
          let res = await getEsonArrayTranslation(esonArr)
          console.log(res.data.data, 'after translation')
          let eng_esonArr = res.data.data
          let json = arr2json(esonArr, iMixMode)
          let eng_json = arr2json(eng_esonArr, iMixMode)
          this.key_setting = JSON.parse(localStorage.getItem('setting'))
          this.code = {
            [this.key_setting.zh]: json,
            [this.key_setting.en]: eng_json
          }
        } catch (e) {
          console.log(e,'-------')
          this.$Message.error(e.message)
        } finally {
          this.multiLoading = false
        }
      },
      // 
      createCollection () {
        let str = this.$refs['eson-editor'].innerText
        let { esonArr, iMixMode } = esonText2arr(str)
        console.log(esonArr, 'esonArr')
        try {
          let collection = validStructure2(esonArr)
          console.log(collection)
          this.code = collection
        } catch (e) {
          console.log(e, 'createCollection fail')
          this.$Message.error(e.message)
        }
      }
    }
  }
</script>

<style scoped>
.button{
  margin-right: 8px;
}
.lan-wrapper{
  display: flex;
}
.lan-left{
  width: 200px;
  background: #FAFAFA;
  cursor: pointer;
}
.lan-middle{
  flex: 1;
  padding: 20px;
  border-right: 1px solid rgb(177, 175, 175);
}
.lan-middle-nocontent{
  padding-top: 160px;
}
.lan-middle-content{
  overflow-y: auto;
  height: 100%;
}
.root-wrapper{
  position: relative;
  text-align: left;
}
.eson-editor-wrapper{
  border: 1px solid #ccc;
  height: 300px;
  text-align: left;
  color: rgb(206, 145, 120);
  background: #1E1E1E;
  font-size: 14px;
  margin-bottom: 10px;
}
.eson-mode{
 /*  position: absolute; */
  width: 100px;
  padding: 10px 0;
  right: 10px;
  top: 10px;
}

.left-header{
  height: 40px;
  line-height: 40px;
  color: #FD783B;
  padding: 0 50px;
  text-align: left;
  border-bottom: 1px solid #ccc;
}
.page-item{
  height: 30px;
  line-height: 30px;
}
.active-page-item{
  background: rgb(185, 182, 182);
}
.edit-table{
  margin: 0 auto 10px auto;
}
.lan-right{
  flex: 1;
  padding: 20px;
}
.code-block{
  height: 100%;
}
.edit-wrapper{
  border: 1px solid #ccc;
  height: 500px;
  text-align: left;
  color: rgb(206, 145, 120);
  background: #1E1E1E;
  font-size: 14px;
  margin-bottom: 10px;
  overflow-y: auto;
}
.eson-editor-wrapper{
  border: 1px solid #ccc;
  height: 300px;
  text-align: left;
  color: rgb(206, 145, 120);
  background: #1E1E1E;
  font-size: 14px;
  margin-bottom: 10px;
  overflow-y: auto;
}
.eson-editor-wrapper:empty::before{
  content: attr(placeholder);
  font-size: 14px;
  color: #CCC;
  line-height: 21px;
  padding-top: 10px;
}
.example-pre{
  background: #333;
  color: white;
  flex: 1;
  margin-left: 20px;
  padding: 20px;
}
.example-input{
  margin: 1em 0;
  width: 200px;
}
</style>



