function assert (value, message) {
  if (!value) {
    throw new Error(message ? message : 'assertion error')
  }
}
function hasRepeatedOrEmptyItem (arr) {
  let json = {}
  for (let i = 0; i < arr.length; i++) {
    if (!arr[i] || json[arr[i]]) {
      return true
    }
    json[arr[i]] = true
  }
  return false
}
// 
export function validStructure2 (arr) {
  assert(arr.length > 1, '2 rows at least')
  //assert(arr[0].indexOf(':') !== -1, "First line must have sign ':'")
  let aKeys = arr[0].split(':')
  aKeys = aKeys.map(item => {
    return item.trim()
  })
  // key
  assert(!hasRepeatedOrEmptyItem(aKeys), 'First Row has Duplicated or Empty Key!')
  let collection = []
  for (let i = 1; i < arr.length; i++) {
    let row = arr[i]
    let aValues = row.split(':')
    let obj = {}
    aKeys.forEach((item, index) => {
      if (aValues[index]) {
        obj[item] = aValues[index]
      }
    })
    collection.push(obj)
  }
  return collection
}


