/*
 *@method 
 *@param{obj}  {}
 *@param{item}  {value: '', key: 'df'}
 *@return {df: ''}objkeydf1
*/
export function extendObj(obj, item) {
  const { key, value } = item
  let suffix = 0
  while (obj[key + (suffix ? suffix : '')]) {
    suffix++;
  }
  const last_key = key + (suffix === 0 ? '' : suffix)
  obj[last_key] = value
  return last_key
}


