<template>
  <div>
    <test :items="a" :x="xx" v-model="huashi" :va="aa"/>
    <div class="wrapper" @click.capture="outclick($event)">
      <h3 @click.prevent="innerclick($event)" id="hh"></h3>
      tryyttttttttttt
      <Modal v-model="b" title="test" ></Modal>
    </div>
    <div contenteditable="true"
      style="width:200px;height:300px;background:#666" @focus="test($event)" @keyup.13.capture="handleEnter($event)">
      <div contenteditable="true"
        style="width:200px;height:100px;background:#6f6" @focus="test($event)"></div>
    </div>
    <div v-contextmenu="folder" class="wrapper">contextmenu</div>
  </div>
</template>

<script>
  import test from '@/components/test'
  import Render from '@/components/Render.js'
  import contextmenu from '@/mixins/contextmenu'
  import axios from 'axios'
  import Vue from 'Vue'
  export default {
    components: {
      test,
      Render
    },
    directives: {
      focus: {
        // 
        inserted: function (el) {
          el.focus()
        }
      },
      contextmenu: {
        bind (el, binding) {
          console.log(el, binding, 'bind')
        },
        inserted (el, binding) {
          console.log(el, binding, 'inserted')
          el.oncontextmenu = function (e) {
            console.log(e)
            return false
          }
        }
      }
    },
    mixins: [contextmenu],
    data () {
      return {
        folder: '111',
        a: [1,2,3,4],
        xx: '99999',
        b: false,
        huashi: '11111113333',
        aa: function (h){
          return h('h2', 'test')
        }
      }
    },
    created (){
      console.log(test.props, this.$children)
    },
    methods: {
      outclick(e){
        // this.b= true
        console.log(e,e.target,'out')
        console.log(11)
        this.$nextTick(() => {
          console.log(111111)
        })
      },
      innerclick(e){
        console.log(e, e.target, 'inner')
      },
      getLabel(h) {
        console.log(h)
        return '@'+ '#'
      },
      test(e){
        console.log(e.target, e, 'focus')
        console.log(test.props, this.$children)
      },
      handleEnter (e) {
        console.log(e.target)
        if(e.target.innerHTML === ''){
          console.log(null)
        }
      }
    },
    mounted () {
      // this.$on('test', function () {
      //   console.log('test is trigger')
      // })
      // this.$emit('test')
      // console.log(document.getElementById('hh'))
      // document.getElementById('hh').style.color = 'red'
      // document.getElementById('hh').click()
    }
  }
</script>

<style scoped>
.wrapper{
  width: 200px;
  height: 300px;
  background: rgb(218, 236, 182);
  /* cursor: not-allowed; */
}
</style>


