<template>
  <ul>
    <li class='name-row' @click='() => itemClick(tree.id)'>{{tree.tag}}</li>
    <ul
      v-if="Array.isArray(tree.children) && tree.children.length > 0"
      style='padding-left: 7px'>
      <li v-for="(item, index) in tree.children" :key='index'>
        <tree-show :tree='item'/>
      </li>
    </ul>
  </ul>
</template>

<script>
import { findComponentUpward } from '../../utils/assist'
export default {
  name: 'TreeShow',
  props: {
    tree: Object,
  },
  // inject: ['easyIviewData'],
  // computed: {
  //   activeRowId() {
  //     return this.easyIviewData.id
  //   }
  // },
  // watch: {
  //   easyIviewData(val) {
  //     console.log(val, 'tree-show')
  //   }
  // },
  methods: {
    itemClick(id) {
      // this.$emit('item-click', id)
      const easyIview = findComponentUpward(this, 'easyIview')
      // console.log(easyIview)
      easyIview.setActiveItem(id)
    }
  }
}
</script>

<style scoped>
li{
  list-style: none;
}
.name-row{
  line-height: 25px;
}
</style>


