<template>
  <div>
    <input type="file" webkitdirectory="" directory="" ref="directory" @change="dirChange" class="line"></br>
    <input type="file" @change="fileChange" ref="file" class="line"></br>
    <input type="file" @change="filesChange" ref="files" multiple class="line">
    <!-- <pre style="text-align:left;background: #666;color: white" v-html="value"></pre> -->
    <div contenteditable="true" class="editor" ref="editor">
      <div class="editor-row"></div>
    </div>
  </div>
</template>

<script>
export default {
  components: {
    
  },
  data () {
    return {
      value: ''
    };
  },

  methods: {
    /* base64 */
    base64Encode (origin) {
      return btoa(encodeURIComponent(origin))
    },
    /* base64 */
    base64Decode (origin) {
      return decodeURIComponent(atob(origin))
    },
    dirChange () {
      let oDir = this.$refs.directory
      console.log(oDir, oDir.files)
    },
    fileChange () {
      let oInput = this.$refs.file
      let files = oInput.files
      console.log(files, files[0].type)
        let reader = new FileReader()
        //reader.readAsDataURL(files[0])
        reader.readAsText(files[0], 'utf-8')
        reader.onload = e => {
          console.log(e,e.target.result.indexOf('\r'), e.target.result.indexOf('\n'),e.target.result.indexOf('\r\n'),)
          // for(let i =0;i<e.target.result.length;i++){
          //   console.log(e.target.result[i])
          // }
          // this.$refs.editor.innerHTML = e.target.result.replace(/\r\n/g, '<br/>')
          // this.value = this.base64Decode(e.target.result.substring(e.target.result.indexOf(',') + 1))
          this.oDom = this.handleFile(e.target.result)
          this.$refs.editor.appendChild(this.oDom)
        }
    },
    filesChange () {
      let oInput2 = this.$refs.files
      console.log(oInput2, oInput2.files)
    },
    handleFile (f) {
      let arr = f.split('\r\n')
      console.log(arr)
      let fragment = document.createDocumentFragment()
      arr.forEach(o => {
        let oDiv = document.createElement('div')
        o = o.replace(/ /g,'&nbsp;')
        oDiv.innerHTML = o
        oDiv.className = 'editor-row'
        fragment.appendChild(oDiv)
      })
      return fragment
    }
  }
}
</script>

<style scoped>
.line{
  margin-top: 20px;
}
.editor{
  text-align: left;
  background: #666;
  color: white;
  min-height: 200px;
}
.editor-row{
  line-height: 22px;
  height: 22px;
  width: 100%;
}
</style>


