<template>
  <div>
    <Modal :title='editorModalTitle' v-model="showEditorModal" @on-ok="handleEditorSwitch">
      <Alert show-icon>
        <p>key-value Editorkey</p>
        <p>Tablecolumns</p>
      </Alert>
      <key-value-editor v-model='editorArray' ref='keyValueEditor' :tip='tip'/>
    </Modal>
    <template v-if="component && component.data.setting">
      <Form :label-width="80" label-position='right'>
        <div v-for="(item, key, index) of component.data.setting">
          <FormItem :label="getLabel(key, item, true)" v-if="item.type === 'number'">
            <InputNumber v-model="item.value" :step='10' :min='1' :placeholder="'Enter   ' + getLabel(key, item)"></InputNumber>
          </FormItem>
          <FormItem :label="getLabel(key, item, true)" v-if="item.type === 'text'">
            <Input v-model="item.value" :placeholder="'Enter   ' + getLabel(key, item)"></Input>
          </FormItem>
          <FormItem :label="key + ':'" v-if="item.type === 'select'">
            <Select v-model="item.value" style="width: 160px" :clearable="!item.required">
              <Option v-for="(option, index) in item.options" :key="index" :value="option">
                {{ option }}
              </Option>
            </Select>
          </FormItem>
          <FormItem :label="key + ':'" v-if="item.type === 'collection'">
            <!-- length -->
            <Badge
              :type="Array.isArray(item.value) && item.value.length > 0 ? 'success' : 'error'"
              :count="item.value.length === 0 ? -1 : item.value.length"
              :dot="item.value.length === 0">
              <a class="demo-badge" @click="test1(item.value, key, item.extra, item.tip)">
                {{
                  item.value.length === 0 ? '' : ''
                }}
              </a>
            </Badge>
          </FormItem>
          <FormItem :label="key + ':'" v-if="item.type === 'switch'">
            <i-switch v-model="item.value">
              <span slot="open">on</span>
              <span slot="close">off</span>
            </i-switch>
          </FormItem>
        </div>
      </Form>
    </template>
    <Button type="primary" @click="useCustomData" v-if="name && component.id !== 1">{{name}}</Button>
  </div>
</template>

<script>
import KeyValueEditor from '@/base/KeyValueEditor'
export default {
  components: {
    KeyValueEditor
  },
  props: {
    name: {
      type: String,
      default: ''
    },
    component: {
      type: Object
    }
  },
  data () {
    return {
      editorType: '',
      editorArray: [],
      editorModalTitle: 'KeyValueEditor',
      extra: '',
      showEditorModal: false, 
      key: '',
      tip: '',
    };
  },

  methods: {
    handleEditorSwitch () {
      let code = this.$refs.keyValueEditor.getCode()
      code = code ? code : []
      this.component.data.setting[this.key].value = code
    },
    useCustomData () {
      const setting = this.component.data.setting
      for (let key in setting) {
        const item = setting[key]
        if (item.required === true) {
          if (item.type === 'text' && item.value.trim() === '') {
            this.$Message.error(`${item.label ? item.label : key}`)
            return
          } else if(item.type === 'collection' && item.value.length === 0) {
            this.$Message.error(`${item.label ? item.label : key}`)
            return
          }
        }
      }
      let _name = this.name.toLowerCase()
      this.$emit('custum-set', this.name, this[_name + 'Setting'])
    },
    test1(array, key, extra, tip){
      console.log(array)
      this.editorArray = array
      this.key = key
      this.editorModalTitle = '' + extra
      this.tip = tip
      this.showEditorModal = true
    },
    getLabel(key, item, showColon) {
      if (showColon) {
        // return h('span', {
        //   class: 'form-label',
        // }, item.label ? item.label + ':' : key + ':')
        return item.label ? item.label + ':' : key + ':'
      } else {
        return item.label ? item.label : key
      }
    }
  }
}
</script>

<style scoped>
.demo-badge{
  margin-left: 5px;
  width: 32px;
  height: 30px;
  background: #eee;
  border-radius: 6px;
  display: inline-block;
}
.form-label::before{
  content: '*',
  color#ed4014,
}
</style>


