export function slot2div(html) {
  // slot tag emplate
  return html ? html.replace(/<(\/)?slot/g, (all, $1) => {
    return `<${$1 ? $1 : ''}div`;
  }) : ''
}

export function camelTag(html) {
  // tagName
  let reg_tag = /(<\/?)i((-\w+){1,})/g
  return html.replace(reg_tag, (all, $1, $2) => {
    return `${$1}${$2.replace(/-([a-z])/g, (all, letter) => {
      return letter.toUpperCase()
    })}`
  })
}
/*
@param html: string
@param tag: string
@param target: string
*/
export function replaceTag(html, tag, target){
  const re_tag = new RegExp(`<(\/)?${tag}`, 'g')
  return html ? html.replace(re_tag, (all, $1) => {
    return `<${$1 ? $1 : ''}${target}`
  }) : ''
}


