<template>
  <div class="buffer-wrapper">
    <Table :columns="columns" :data="data">
      <p slot-scope="{ row, index }" slot="name">
      </p>
      <template slot-scope="{ row, index }" slot="age">
        {{ row.age }}
      </template>
    </Table>
    <!-- <Card>
      <div slot='title'>title</div>
      <div>
        <div slot='title'>title11</div>
      </div>
      <test x='slot-scope'>
        <template v-slot:default='{x, y}'>wew
          x:{{x}}{{y}}
        </template>
      </test>
      <Descriptions title='Descriptions'>
        <DescriptionItem label=''></DescriptionItem>
        <DescriptionItem label='df'></DescriptionItem>
        <DescriptionItem label='fdf'>e</DescriptionItem>
        <DescriptionItem label=''>f</DescriptionItem>
        <DescriptionItem label='vfcv'>fd</DescriptionItem>
        <DescriptionItem label='vc'></DescriptionItem>
        <DescriptionItem label='vb'></DescriptionItem>
        <DescriptionItem label='d'></DescriptionItem>
      </Descriptions>
    </Card> -->
    <div class="buffer-left">
      <Input v-model="custom_input" type="textarea" placeholder="Enter something..." :rows="16"/>
      <div class="left-button-wrapper">
        <Button type="primary" @click="clear"></Button>
        <Button type="primary" @click="buffer2string">Bufferstring</Button>
        <Button type="primary" @click="uni2zh">Unicode</Button>
      </div>
    </div>
    <div class="buffer-right">
      <Icon type="ios-arrow-back" class="back-arrow" size="20" @click="shift2left"/>
      <Input v-model="custom_output" type="textarea" placeholder="result..." :rows="16"/>
    </div>
  </div>
</template>

<script>
import localforage from 'localforage'
import PageApi from '../apis/PageApi'
import test from '@/components/test'
export default {
  components: {
    test
  },
  data () {
    return {
      custom_input: '',
      custom_output: '',
      editIndex: -1,  // 
      editName: '',
      columns: [
          {
            title: '',
            slot: 'name'
          },
          {
            title: '',
            slot: 'age'
          },
          {
            title: '',
            slot: '',
            key: 'action'
          }
        ],
        data: [
          {
            name: '',
            age: 18,
            birthday: '919526400000',
            address: ''
          },
          {
            name: '',
            age: 25,
            birthday: '696096000000',
            address: ''
          },
        ],
    };
  },
  async created() {
    let pageApi = new PageApi()
    // pageApi.addPage({data:{a:12112}}).then().catch(e=> {console.error(e, 11111)})
    // pageApi.addPage({data:{a:333333}})
    // pageApi.addPage({data:{a:333333}})
    // pageApi.deletePage('54b70ee4-b472-470f-9992-e884a01000ba')
    // console.log(await pageApi.getAllPages())
    // var page = localforage.createInstance({
    //   name: "page"
    // }).then((a) => {console.log(a)}).catch((e)=> {e});
    // console.log(await page.keys())
  },
  methods: {
    clear(){
      this.custom_input = ''
      this.custom_output = ''
      let re = /'(this.\$l\('\w+'\))'/g;
      let str = "[{'name':'a','label':'this.$l('name')'},{'name':'d','label':'this.$l('path')'}]";
      console.log(str.search(re))
      let x = str.replace(re, (all, $1) => {
        console.log($1, '111111');
        return $1;
      })
      console.log(x, 'xxxxxxxxxx');
    },
    buffer2string () {
      if(this.custom_input === ''){
        this.$Message.error('buffer()')  // JSON.parse
        return
      }
      try{
        let buffer = new Buffer(JSON.parse(this.custom_input)) //[0xE4,0xB8,0xAD]
        this.custom_output = buffer.toString()
        console.log(this.custom_output)
      }catch(e){
        this.custom_output = ''
        this.$Message.error('buffer()')
      }
      
    },
    shift2left(){
      this.custom_input = this.custom_output
      this.custom_output = ''
    },
    uni2zh(){
      // this.custom_output = decodeURI(encodeURI(this.custom_input))
      this.custom_output = this.decodeUnicode(this.custom_input)
    },
    decodeUnicode (str) {
      str = str.replace(/\\/g, '%')
      return unescape(str)
    }
  }
}
</script>

<style scoped>
.buffer-wrapper{
  padding: 20px;
  display: flex;
}
.buffer-left{
  flex: 1;
  margin-right: 100px;
}
.left-button-wrapper{
  margin-top: 20px;
}
.buffer-right{
  position: relative;
  flex: 1;
}
.back-arrow{
  position: absolute;
  top: 150px;
  left: -60px;
  padding: 5px;
  background: rgb(175, 172, 172);
}
</style>


